import {Component} from '@angular/core';
import {App, AlertController, NavController, ToastController} from 'ionic-angular';
import {PaymentPage} from "../payment/payment";
import {HelpPage} from "../help/help";
import {SettingsPage} from "../settings/settings";
import {AddressPage} from "../address/address";
import {FavoritePage} from "../favorite/favorite";
import {UsersProvider } from '../../providers/users/users';
import {TermsPage} from "../terms/terms";
import {LeadPage} from "../lead/lead";
import {DigitalWalletPage} from "../digital-wallet/digital-wallet";

import {
  LoginService
} from "../../services/login-service";

import {
  LoginPage
} from "../login/login"; 

import { StatusBar } from '@ionic-native/status-bar';

@Component({
  selector: 'page-user',
  templateUrl: 'user.html',
})
export class UserPage {
  // username
  username = 'Perfil';
  isLogged:boolean = false;
  constructor(public nav: NavController, 
    public app: App,
    private alertCtrl: AlertController,
    private loginService: LoginService,
    private toastCtrl: ToastController,
    private statusBar: StatusBar,
    private usersProvider: UsersProvider) {
    // let status bar overlay webview
    this.statusBar.overlaysWebView(false);

    // set status bar to white
    this.statusBar.backgroundColorByHexString('#ffb42d');
  }

  // got to payment setting
  goToPayment() {
    this.app.getRootNav().push(PaymentPage);
  }

  // go to help page
  goToHelp() {
    this.app.getRootNav().push(HelpPage);
  }

  goToFavorite() {
    this.app.getRootNav().push(FavoritePage);
  }

  // go to setting page
  goToSetting() {
    this.app.getRootNav().push(SettingsPage)
  }

  // go to setting page
  goToDigitalWallet() {
    this.app.getRootNav().push(DigitalWalletPage)
  }

  // go to setting page
  goToAddress() {
    this.app.getRootNav().push(AddressPage)
  }

  // go to lead page
  goToLead() {
    this.app.getRootNav().push(LeadPage);
  }

  // go to term page
  getTerm() {
    this.app.getRootNav().push(TermsPage);
  }

  // go to login page
  goToLogout() {
    this.presentConfirm();
  }

  // Remove o usuário do banco de dados local
  private updateUser() {
    return this.usersProvider.update({id: 1, json: '{}'});
  }

  presentToast(message) {
    let toast = this.toastCtrl.create({
      message: message,
      duration: 3000,
      position: 'top'
    });

    toast.present();
  }

  ionViewWillEnter() {
    this.isLogged = this.loginService.isLogged();
    if(!this.isLogged){
      this.nav.push(LoginPage, {
        backButton: true
      });
      this.username = 'Perfil';
    }else{
      let user = this.loginService.getUser();
      this.username = user.first_name + ' ' + user.last_name;
    }
  }

  presentConfirm() {
    let alert = this.alertCtrl.create({
      title: 'Você tem certeza?',
      message: 'Deseja realmente sair?',
      buttons: [{
        text: 'Não',
        role: 'cancel',
        handler: () => {
          console.log('Cancel clicked');
        }
      }, {
        text: 'Sim',
        handler: () => {
          this.loginService.setUser({});
          this.updateUser();
          this.presentToast("Usuário deslogado");
          this.nav.push(LoginPage, {
            backButton: true
          });
        }
      }]
    });
    alert.present();
  }


}


