  import {Injectable} from "@angular/core";
import { Http, Headers } from '@angular/http';
import 'rxjs/Rx';
import { Observable } from 'rxjs';

import {CONFIG} from "./config";

import {LoginService} from "./login-service";

@Injectable()
export class HistoryOrderService {
  private history:any;

  constructor(public http: Http,
    public loginService:LoginService) {
    this.history = [];
  }

  getByUser() {
    let id = this.loginService.getUser().id;
    let headers = new Headers();
    headers.append('authorization', this.getToken());
    return this.http.get(CONFIG.url + "/orders/user/"+id, {headers: headers})
      .map(res => {
        this.history = res.json().data;
        return res.json();
      })
      .catch(error => {
        return Observable.throw(error);
      });
  }

  getRefreshById(orderId) {
    let headers = new Headers();
    headers.append('authorization', this.getToken());
    return this.http.get(CONFIG.url + "/orders/"+orderId, {headers: headers})
      .map(res => {
        let order = res.json().data;
        for (let i = 0; i < this.history.length; i++) {
          if(this.history[i].id === order.id){
            this.history[i] = order;
          }
        }
        return res.json();
      })
      .catch(error => {
        return Observable.throw(error);
      });
  }

  cancelOrder(order){
    let headers = new Headers();
    headers.append('authorization', this.getToken());
    return this.http.put(CONFIG.url + "/orders/cancel", order, {headers: headers})
      .map(res => {
        return res.json();
      })
      .catch(error => {
        return Observable.throw(error);
      });
  }

  getById(id) {
    for (let i = 0; i < this.history.length; i++) {
      if(this.history[i].id+"" === id+""){
        return this.history[i];
      }
    }
  }

  getAll(){
  	return this.history;
  }

  getToken() {
    return this.loginService.getToken();
  }

}



