import {
  Injectable
} from "@angular/core";
import {
  CONFIG
} from "./config";
import {
  Http, Headers
} from '@angular/http';
import 'rxjs/Rx';

import {
  Geolocation
} from '@ionic-native/geolocation';
import {
  LocationAccuracy
} from '@ionic-native/location-accuracy';

import {
  UsersProvider
} from '../providers/users/users';

import { Observable } from 'rxjs';

@Injectable()
export class LoginService {

  private user: any;
  private location: any;
  constructor(private geolocation: Geolocation,
    public http: Http,
    private locationAccuracy: LocationAccuracy,
    private usersProvider: UsersProvider) {
    this.http = http;
    this.user = {};

  }

  getLocation() {
    this.locationAccuracy.canRequest().then((canRequest: boolean) => {

      if (canRequest) {
        // the accuracy option will be ignored by iOS
        this.locationAccuracy.request(this.locationAccuracy.REQUEST_PRIORITY_HIGH_ACCURACY).then(
          () => {
            this.location = {};
            this.geolocation.getCurrentPosition().then((resp) => {
              this.location.latitude = resp.coords.latitude;
              this.location.longitude = resp.coords.longitude;
              if (this.location.latitude && this.location.longitude) {
                this.getAddress(this.location.latitude, this.location.longitude).subscribe();
              }
            }).catch((error) => {
              console.log('Error getting location', error);
            });
          },
          error => console.log('Error requesting location permissions', error)
        );
      }

    });
  }

  login(profile) {
    return this.http.post(CONFIG.url + "/authenticate", profile)
      .timeout(10000)
      .map(res => {
        let result = res.json();
        if (result.status) {
          this.user = res.json().data;
          this.user.location = this.location;
          this.user.favorites_restaurants_ids = JSON.parse(this.user.favorites_restaurants_ids);
          this.user.token = res.json().token;
        }
        return res.json()
      })
      .catch(error => {
        return Observable.throw(error);
      });
  }

  loginExternal(profile) {
    return this.http.post(CONFIG.url + "/loginfacebook", profile)
      .timeout(10000)
      .map(res => {
        this.user = res.json().data;
        this.user.location = this.location;
        this.user.favorites_restaurants_ids = JSON.parse(this.user.favorites_restaurants_ids);
        this.user.token = res.json().token;
        return res.json()
      })
      .catch(error => {
        return Observable.throw(error);
      });
  }

  setUser(user) {
    this.user = user;
    this.setLocation(this.location)
  }

  getUser() {

    return this.user;
  }

  getUserLocation() {

    // Caso não tenha location, busca o primeiro endereço cadastrado
    if((!this.user.location || !this.user.location.latitude) && this.user.Addresses && this.user.Addresses.length){
      let address = this.user.Addresses[0];
      let location = LoginService.mountLocation(address);
      this.setLocation(location);
      return location;
    }
    return this.user.location;
  }

  setFavoritesRestaurantsIds(favorites) {
    this.user.favorites_restaurants_ids = favorites;
  }

  updateUser(profile) {
    let headers = new Headers();
    headers.append('authorization', this.getToken());
    return this.http.put(CONFIG.url + "/users", profile, {headers: headers})
      .timeout(10000)
      .map(res => {
        let user = res.json().data;
        this.user.firstName = user.firstName;
        this.user.lastName = user.lastName;
        this.user.country_code = user.country_code;
        this.user.phone = user.phone;
        res.json().data = this.user;
        return res.json();
      })
      .catch(error => {
        return Observable.throw(error);
      });
  }

  setLocation(location) {
    if(location) {
      this.user.location = location;
      this.location = location;
      this.setUserDatabase({
        id: 1,
        json: JSON.stringify(this.user)
      });
    }
  }

  setNewUser(status) {
    this.user.isNew = status;
    this.setUserDatabase(this.user);
  }

  setActivation(code, status) {
    this.user.code = code;
    this.user.activation_code = status;
    this.setUserDatabase(this.user); 
  }

  // Busca o endereço pela Latitude e Longitude
  getAddress(lat, lng) {
    return this.http.get("https://maps.googleapis.com/maps/api/geocode/json?latlng=" + lat + "," + lng + "&sensor=true&key=AIzaSyD1PPrTt9S6nYT-wPFs0q4mIpbY3XyAjqA")
      .timeout(10000)
      .map(res => {
        let formatJson = JSON.parse(res["_body"]);

        // Convert resposta da API do google em um objeto
        let address: { street: string; complement: null; neighborhood: any; city: any; state: any; cep: any; latitude: number | any; longitude: number | any };
        
        // Convert resposta da API do google em um objeto
        if(formatJson.results.length){
          address = this.mountAddress(formatJson.results[0].address_components, {
            latitude: lat,
            longitude: lng
          });
        }

        // Seta o endereço recebido da API
        this.location = address;

        return res;
      })
      .catch(error => {
        return Observable.throw(error);
      });
  }

  getToken() {
    return this.user.token;
  }

  // Salva o usuário no banco de dados local
  setUserDatabase(model) {
    return this.usersProvider.update(model);
  }

  //Monta o objeto endereço apartir do dados retornados da API do google
  mountAddress(address, location) {
    /*
    [0] -> Número
    [1] -> Rua/Avenida
    [2] -> Bairo
    [3] -> Cidade
    [5] -> Estado
    [6] -> Area level 2
    [7] -> ZipCode
    */
    return {
      street: address[1].long_name + " • " + address[0].long_name,
      complement: null,
      neighborhood: address[2].long_name,
      city: address[3].long_name,
      state: address[5].long_name,
      cep: address[6].long_name,
      latitude: location.latitude,
      longitude: location.longitude
    }
  }

  // Monta o objeto location apartir dos dados de endereço
  static mountLocation(address) {
    return {
          latitude: address.latitude,
          longitude: address.longitude,
          street: address.street + ", " + address.number,
          complement: address.complement,
          neighborhood: address.neighborhood,
          city: address.city,
          state: address.state,
          cep: address.cep
      };

  }

  isLogged() {
    return this.user && this.user.first_name ? true : false;
  }

}



