import { Component } from '@angular/core';
import {
  App,
  AlertController,
  NavController,
  LoadingController,
  ToastController
} from 'ionic-angular';

import {
  RestaurantService
} from '../../services/restaurant-service';

import {
  FormBuilder,
  Validators
} from '@angular/forms';

import { GoogleAnalytics } from '@ionic-native/google-analytics';

import {
  CheckInternet
} from '../../services/check-internet';

import {
  WithoutInternetPage
} from "../without-internet/without-internet";

import { StatusBar } from '@ionic-native/status-bar';
/**
 * Generated class for the LeadPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@Component({
  selector: 'page-lead',
  templateUrl: 'lead.html',
})
export class LeadPage {

    register: any;
    restaurant: any;

	constructor(public app: App,
    public nav: NavController,
		private toastCtrl: ToastController,
		private alertCtrl: AlertController,
		private loadingCtrl: LoadingController,
		public ga: GoogleAnalytics,
		public restaurantService: RestaurantService,
    private checkInternet: CheckInternet,
    private statusBar: StatusBar,
		public formBuilder: FormBuilder) {

    // let status bar overlay webview
    this.statusBar.overlaysWebView(false);

    // set status bar to white
    this.statusBar.backgroundColorByHexString('#ffb42d');

	 this.ga.trackView('Tela Sugerir Restaurante');

	this.restaurant = {
    country_code: 55
  };
	this.register = this.formBuilder.group({
			name: ['', Validators.required],
			country_code: [''],
			phone: [''],
			city: [''],
      street: [''],
      number: [''],
      neighborhood: [''],
      state: ['']
		});
	}

  ionViewDidEnter() {
    // Antes de tudo vericar se usuário possui Internet
    if (!this.checkInternet.isInternet()) {
      this.withoutInternet();
      return false;
    }
  }

  save() {

    // Antes de tudo vericar se usuário possui Internet
    if (!this.checkInternet.isInternet()) {
      return false;
    }

    this.ga.trackEvent('Click', 'Botão Sugerir Restaurante', null, null);
    // Create the popup
    let loader = this.loadingCtrl.create({
      content: 'Aguarde...'
    });
    // Show the popup
    loader.present();
    loader.present().then(() => {
      this.restaurantService.lead(this.restaurant).subscribe(result => {
        // remove the popup
        loader.dismiss();
        if (result.status) {
          this.register.reset();
          this.presentToast(result.message);
        } else {
          this.presentAlert("Alerta", result.message)
        }
      }, error => {
        console.log(error);
        // remove the popup
        loader.dismiss();
      });
    });
  }

    presentAlert(title,subTitle) {
	    let alert = this.alertCtrl.create({
	      title: title,
	      subTitle: subTitle,
	      buttons: ['Ok']
	    });
	    alert.present();
	}

	presentToast(message) {
	    let toast = this.toastCtrl.create({
	      message: message,
	      duration: 3000,
	      position: 'top'
	    });

	    toast.present();
	}

  // Tela de sem acesso a internet
  withoutInternet() {
    this.app.getRootNav().push(WithoutInternetPage);
  }

}



