import { Component } from '@angular/core';
import { AlertController, LoadingController, NavController, NavParams, ToastController } from 'ionic-angular';

import {
  LoginService
} from '../../services/login-service';

import {
  UserService
} from '../../services/user-service';

import { GoogleAnalytics } from '@ionic-native/google-analytics';

import { StatusBar } from '@ionic-native/status-bar';
/**
 * Generated class for the SmsPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@Component({
  selector: 'page-sms',
  templateUrl: 'sms.html',
})
export class SmsPage {

	profile: any;
	
	codeReceived:any;
	constructor(public navCtrl: NavController, 
		public navParams: NavParams,
		private alertCtrl: AlertController,
		private toastCtrl: ToastController,
		private ga: GoogleAnalytics,
		private statusBar: StatusBar,
		private loginService: LoginService,
		private loadingCtrl: LoadingController,
		private userService: UserService) {

		// let status bar overlay webview
		this.statusBar.overlaysWebView(false);

		// set status bar to white
		this.statusBar.backgroundColorByHexString('#ffb42d');
		
		this.ga.trackView('Tela SMS');

		let user = this.loginService.getUser();

		if(!user.amount_requests || !isNaN(user.amount_requests)){
			user.amount_requests = 0;
		}

		this.profile = {
			country_code: "+55",
			phone: user.phone ? user.phone : '',
			amount_requests: user.amount_requests
		};
		this.codeReceived = {
			code: user.code,
			phone: user.phone
		}
	}

	ionViewDidLoad() {
		console.log('ionViewDidLoad SmsPage');
	}

	sendSms() {

		if(!this.profile.phone || this.profile.phone.length < 10){
			this.presentAlert("Alerta", "Insira o número telefônico");
		}
		this.ga.trackEvent('Click', 'Botão envia sms', null, null);
		
		let loader = this.loadingCtrl.create({
	      content: 'Enviando...'
	    });
		
	    // Show the popup
	    loader.present().then(() => {

        	this.userService.sendSms(this.profile.country_code + this.profile.phone).subscribe(result => {
				if(result.status){
					this.codeReceived = result.data;
					this.codeReceived = result.data;
					this.profile.amount_requests = result.data.amount_requests;
					this.presentToast(result.message);
					this.userService.setPhone(this.profile.country_code, this.profile.phone);
				}else{
					this.presentAlert("Alerta", result.message);
				}
				loader.dismiss();
			}, erro => {
				loader.dismiss();
			});
        
    	});

		
	}

	validCode() {
		if(this.profile.code === this.codeReceived.code+""){
			this.ga.trackEvent('Click', 'Botão valida codigo sms', null, null);

			let loader = this.loadingCtrl.create({
		      content: 'Validando...'
		    });

		    // Show the popup
		    loader.present().then(() => {
				this.userService.updateActivationPhone().subscribe(result => {
					loader.dismiss();
					if(result.status){
						this.presentToast("Pronto, agora é só escolher sua comida.");
						this.loginService.setActivation(this.profile.code, true);
						this.closeModal();
					}else{
						this.presentAlert("Alerta", result.message);
					}
				}, error => {
					loader.dismiss();
				});
	        
	    	});
		}else{
			this.presentAlert("Alerta", "Código inválido!");
		}
	}

	presentAlert(title, subTitle) {
	    let alert = this.alertCtrl.create({
	      title: title,
	      subTitle: subTitle,
	      buttons: ['Ok']
	    });
	    alert.present();
	}

	presentToast(message) {
	    let toast = this.toastCtrl.create({
	      message: message,
	      duration: 3000,
	      position: 'top'
	    });

	    toast.present();
  	}

  	closeModal() {
        this.navCtrl.pop();
    }

}



