import {Component} from '@angular/core';
import {AlertController, NavController, NavParams, LoadingController, ToastController} from 'ionic-angular';
import {FormBuilder, Validators} from '@angular/forms';

import {AddressService} from '../../../services/address-service';

import {
  LoginService
} from "../../../services/login-service";

import {
  MainTabsPage
} from "../../main-tabs/main-tabs";

import { StatusBar } from '@ionic-native/status-bar';

/*
  Generated class for the LoginPage page.

  See http://ionicframework.com/docs/v2/components/#navigation for more info on
  Ionic pages and navigation.
*/
@Component({
  selector: 'page-new-address',
  templateUrl: 'new-address.html',
})
export class NewAddressPage {

 	  address:any;
    // Caso não saiba o endereço
    auxAddress:any;
    register: any;

    text:string;
    noHaveCep:boolean = false;
  	constructor(public nav: NavController,
                public navParams: NavParams,
                public formBuilder: FormBuilder,
                private alertCtrl: AlertController,
                private loadingCtrl: LoadingController,
                private toastCtrl: ToastController,
                private statusBar: StatusBar,
                private loginService: LoginService,
                public addressService: AddressService) {

      // let status bar overlay webview
      this.statusBar.overlaysWebView(false);

      // set status bar to white
      this.statusBar.backgroundColorByHexString('#ffb42d');

      this.auxAddress = {}
      this.text = navParams.get('address') ? 'Atualizar Endereço' : 'Novo Endereço';
      this.address = navParams.get('address') ? JSON.parse(navParams.get('address')) : {};
      this.address.users_id = this.address.users_id ? this.address.users_id : navParams.get('userId');

      this.register = this.formBuilder.group({
        city: ['', Validators.compose([Validators.minLength(2), Validators.required])],
        street: ['', Validators.compose([Validators.minLength(2), Validators.required])],
        number: ['', Validators.compose([Validators.minLength(1), Validators.required])],
        neighborhood: ['', Validators.compose([Validators.minLength(3), Validators.required])],
        complement: ['']
      });
  	}

  	addOrEdit(){
      if(!this.address.id){
        this.save(this.address);
      }
  	}

    save(address){
      // Create the popup
      let loader = this.loadingCtrl.create({
        content: 'Carregando...'
      });
      // Show the popup
      loader.present();
      loader.present().then(() => {        
        this.addressService.save(address).subscribe(data => {
              // remove the popup
              loader.dismiss();
              if(data.status){
                this.presentToast(data.message);
                this.nav.pop();
              }else{
                this.presentAlert(data.message)
              }
           }, error => {
               console.log(error);
               // remove the popup
              loader.dismiss();
           });
      });
    }

  // Remove o endereço pelo ID
  remove(id){
      // Create the popup
      let loader = this.loadingCtrl.create({
        content: 'Removendo...'
      });
      // Show the popup
      loader.present();
      loader.present().then(() => {        
        this.addressService.remove(id).subscribe(data => {
              // remove the popup
              loader.dismiss();
              if(data.status){
                this.presentToast(data.message);
                this.address = {};
                this.text = 'Novo Endereço';
              }else{
                this.presentAlert(data.message);
              }
           }, error => {
               console.log(error);
               // remove the popup
              loader.dismiss();
           });
      });
  }

  // Busca o endereço no serviço dos correios através do CEP inserido.
  findByCep() {
      if(!this.address.cep){
        this.presentAlert("O campo CEP é obrigatório.");
        return;
      }
      if(!this.address.number){
        this.presentAlert("O Número é obrigatório.");
        return;
      }
      // Create the popup
      let loader = this.loadingCtrl.create({
        content: 'Buscando endereço...'
      });
      // Show the popup
      loader.present();
      loader.present().then(() => {        
        this.addressService.findByCep(this.address.cep).subscribe(result => {
              // remove the popup
              loader.dismiss();
              if(result.status){
                let data = result.data;
                this.address.street = data.logradouro;
                this.address.neighborhood = data.bairro;
                this.address.city = data.localidade;
                this.address.state = data.uf;
                this.findLatAndLngByAddress();
              }else{
                this.presentAlert(result.message);
              }
           }, error => {
               console.log(error);
               // remove the popup
              loader.dismiss();
           });
      });
  }

  findByWithoutCep() {
      // Create the popup
      let loader = this.loadingCtrl.create({
        content: 'Buscando endereço...'
      });
      // Show the popup
      loader.present();
      loader.present().then(() => {        
        this.addressService.findLatAndLngByAddress(this.auxAddress).subscribe(result => {
          loader.dismiss();
          if(result && result.results && result.results[0] && result.results[0].geometry && result.results[0].geometry.location){
            this.address.street = this.auxAddress.street;
            this.address.neighborhood = this.auxAddress.neighborhood;
            this.address.number = this.auxAddress.number;
            this.address.complement = this.auxAddress.complement;
            // Separa "Araguari - MG" -> [0] = Araguari  [1] = MG
            var aux = this.auxAddress.city.split("-");

            this.address.city = aux[0];
            this.address.state = aux[1];
            this.address.latitude = result.results[0].geometry.location.lat;
            this.address.longitude = result.results[0].geometry.location.lng;
            this.address.cep = result.results[0].address_components[result.results[0].address_components.length-1].long_name;
            this.findByCep();

            // Create the popup
            let loader = this.loadingCtrl.create({
              content: 'Buscando endereço...'
            });
            // Show the popup
            loader.present();
            loader.present().then(() => {        
              this.addressService.findByCep(this.address.cep).subscribe(result => {
                    // remove the popup
                    loader.dismiss();
                    if(result.status){
                      let data = result.data;
                      this.address.street = data.logradouro;
                      this.address.neighborhood = data.bairro;
                      this.address.city = data.localidade;
                      this.address.state = data.uf;
                      this.iHaveCep();
                    }else{
                      //this.presentAlert(result.message);
                    }
                 }, error => {
                     console.log(error);
                     // remove the popup
                    loader.dismiss();
                 });
            });
          }else{
            this.presentToast("Verifique o seu número e cep foi digitado corretamente.");
          }
        });
      });
  }

  // Busca a latitude e longitude pelo endereço
  findLatAndLngByAddress() {
    if(this.address.number && this.address.number.length && this.address.cep && this.address.cep.length){
      this.addressService.findLatAndLngByAddress(this.address).subscribe(result => {
        if(result && result.results && result.results[0] && result.results[0].geometry && result.results[0].geometry.location){
          this.address.latitude = result.results[0].geometry.location.lat;
          this.address.longitude = result.results[0].geometry.location.lng;
        }else{
          this.presentToast("Verifique o seu número e cep foi digitado corretamente.");
        }
      })
    }
  }

  goToMain() {
    this.nav.setRoot(MainTabsPage);
  }

  // Seta o endereço escolhido para buscar os restaurantes que estão proximos do endereço selecionado
  setAddress(address) {
    let location = {
      latitude: address.latitude,
      longitude: address.longitude,
      street: address.street + ", " + address.number,
      complement: address.complement,
      neighborhood: address.neighborhood,
      city: address.city,
      state: address.state,
      cep: address.cep
    }
    this.loginService.setLocation(location);
    this.presentToast("Endereço foi selecionado");
    this.goToMain();
  }

  presentAlert(subTitle) {
    let alert = this.alertCtrl.create({
      title: 'Alerta',
      subTitle: subTitle,
      buttons: ['Ok']
    });
    alert.present();
  }

  presentToast(message) {
      let toast = this.toastCtrl.create({
        message: message,
        duration: 3000,
        position: 'top'
      });

      toast.present();
  }

  dontNowMyCep() {
    this.noHaveCep = true;
    this.auxAddress = {}
  }

  iHaveCep() {
    this.noHaveCep = false;
  }

}


