import {Injectable} from "@angular/core";
import { Http, Headers } from '@angular/http';
import 'rxjs/Rx';

import {Observable} from 'rxjs/Observable';

import {CONFIG} from "./config";

import {LoginService} from "./login-service";

@Injectable()
export class AddressService {
  private address:any;

  constructor(public http: Http,
    public loginService:LoginService) {
    this.address = [];
  }

  getAll(){
  	return this.address;
  }

  findById(id) {
    let headers = new Headers();
    headers.append('authorization', this.getToken());
    return this.http.get(CONFIG.url + "/address/"+id, {headers: headers})
      .map(res => {
        this.address = res.json().data;
        return res.json();
      })
      .catch(error => {
        return Observable.throw(error);
      });
  }

  // Busca o endereço pelo CEP -> Ex: 38440-046 ou 38440046
  findByCep(cep) {
    let headers = new Headers();
    headers.append('authorization', this.getToken());
    return this.http.get(`${CONFIG.url}/correios/cep/${cep}`, {headers: headers})
      .map(res => {
        return res.json();
      })
      .catch(error => {
        return Observable.throw(error);
      });
  }

  // Apartir do endereço (Rua + Numero + Bairro + Cidade + Estado) busca a latitude e longitude
  findLatAndLngByAddress(object):Observable<any> {
    let state = object.cep ? object.state + " " + object.cep : "";
    console.log(object.street + " " + object.number + "+" + object.neighborhood + " " + object.city + " " + state);
    let address = encodeURI(object.street + " " + object.number + " " + object.neighborhood + " " + object.city + " " + state);
    console.log(address);
    return this.http.get("https://maps.googleapis.com/maps/api/geocode/json?address="+address+"&key=AIzaSyD1PPrTt9S6nYT-wPFs0q4mIpbY3XyAjqA")
      .map(res => {
        return res.json();
      })
      .catch(error => {
        return Observable.throw(error);
      });
  }

  save(address){
    let headers = new Headers();
    headers.append('authorization', this.getToken());
  	return this.http.post(CONFIG.url + "/address", address, {headers: headers})
      .map(res => {
        this.address.push(res.json().data);
        return res.json();
      })
      .catch(error => {
        return Observable.throw(error);
      });
  }

  update(address){
    let headers = new Headers();
    headers.append('authorization', this.getToken());
  	return this.http.put(CONFIG.url + "/address", address, {headers: headers})
      .map(res => {
      	let address = res.json().data;
      	for (let i = 0; i < this.address.length; i++) {
      		if(this.address[i].id === address.id){
        		this.address[i] = address;
        	}
      	}
        return res.json();
      })
      .catch(error => {
        return Observable.throw(error);
      });
  }

  remove(id){
    let headers = new Headers();
    headers.append('authorization', this.getToken());
  	return this.http.delete(CONFIG.url + "/address/"+id, {headers: headers})
      .map(res => {
      	for (let i = 0; i < this.address.length; i++) {
      		if(this.address[i].id === id){
        		this.address.splice(i, 1);
        	}
      	}
        return res.json();
      })
      .catch(error => {
        return Observable.throw(error);
      });
  }

  getToken() {
    return this.loginService.getToken();
  }

}



