import {
	Component
} from '@angular/core';
import {
	LoadingController,
	NavController,
	NavParams,
	ToastController
} from 'ionic-angular';
import {
  FormBuilder,
  Validators
} from '@angular/forms';
import {
	RestaurantService
} from '../../services/restaurant-service';

import {
	CartService
} from '../../services/cart-service';

import {
	LoginService
} from '../../services/login-service';

import {
  LoginPage
} from "../login/login";

import {UsersProvider } from '../../providers/users/users';

import { StatusBar } from '@ionic-native/status-bar';

/**
 * Generated class for the RatingPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@Component({
	selector: 'page-rating',
	templateUrl: 'rating.html',
})
export class RatingPage {

	register: any;
	restaurant:any;
	order:any; 
	constructor(public nav: NavController, 
		public navParams: NavParams,
		public toastCtrl: ToastController,
		private loadingCtrl: LoadingController,
		private statusBar: StatusBar,
		private restaurantService: RestaurantService,
		private loginService: LoginService,
		private cartService: CartService,
		private usersProvider: UsersProvider,
		public formBuilder: FormBuilder) {

		// let status bar overlay webview
		this.statusBar.overlaysWebView(false);

		// set status bar to white
		this.statusBar.backgroundColorByHexString('#ffb42d');

		this.order = JSON.parse(navParams.get('order'));
		this.restaurant = this.order.orders.restaurant;

		this.register = this.formBuilder.group({
	      rate_food: ['', Validators.required],
	      rate_custo: ['', Validators.required],
	      rate_packing: ['', Validators.required],
	      rate_delivery: ['', Validators.required],
	      description: ['']
	    });

		// Verifica se cliente foi buscar "0"
	    if(parseInt(this.order.flag_delivery) < 1){
	    	this.register.controls['rate_delivery'].setValue(0);
	    }
	}

	rating(rate, description){
		
		let user = this.loginService.getUser();
		let userString = {
			img: user.img,
			first_name: user.first_name,
			last_name: user.last_name,
			email: user.email,
			country_code: user.country_code,
			phone: user.phone,
		}

		let json = {
			restaurants_id: this.restaurant.id,
			rate_food: this.register.get('rate_food').value,
			rate_custo: this.register.get('rate_custo').value,
			rate_delivery: this.register.get('rate_delivery').value,
			rate_packing: this.register.get('rate_packing').value,
			description: this.register.get('description').value,
			user: JSON.stringify(userString)
		}

		// Create the popup
	    let loader = this.loadingCtrl.create({
	      content: 'Aguarde...'
	    });
	    // Show the popup
	    loader.present();
	    loader.present().then(() => {

	    	this.restaurantService.rating(json).subscribe(result => {
	    		loader.dismiss();
				if(result.status){
					this.updateRating(json);
					this.toast(result.message)
					this.nav.pop();
				}else{
					this.toast(result.message)
				}
			}, error => {
				loader.dismiss();
		        console.log(error);
		        if(error.name === "TimeoutError"){
		            this.presentToast('Tente novamente!');
		        }
	          	if(error.status === 401){
		            this.presentToast("Sua sessão expirou");
		            this.goToLogout();
	          	}
		      });
		    });
	}

	toast(message){
		let toast = this.toastCtrl.create({
	      message: message,
	      duration: 3000,
	      position: 'top'
	    });
	    toast.present();
	}

	updateRating(rating){
		this.order.orders.rating = rating;
		let json = {
			id: this.order.id,
			orders: JSON.stringify(this.order.orders),
		};
		this.cartService.updateRating(json).subscribe(result => {

		})
	}

	presentToast(message) {
	    let toast = this.toastCtrl.create({
	      message: message,
	      duration: 3000,
	      position: 'top'
	    });

	    toast.present();
	}

	// go to login page
	goToLogout() {
	    this.updateUser();
	    this.nav.push(LoginPage);
	}

	// Remove o usuário do banco de dados local
	private updateUser() {
	    return this.usersProvider.update({id: 1, json: '{}'});
	}

}


