import {Injectable} from "@angular/core";
import {LoginService} from "./login-service";
import {CategoryService} from "./category-service";
import {CONFIG} from "./config";
import { Http, Headers } from '@angular/http';
import { Observable } from 'rxjs';

@Injectable()
export class RestaurantService {
  private restaurants:any;
  private favoritesRestaurants:any;
  private offset:number = 0;
  private haveMore:boolean;
  constructor(public loginService:LoginService,
              public categoryService:CategoryService,
              public http: Http) {
    this.haveMore = true;
    this.restaurants = [];
  }

  // Inicializa pelo home.ts
  getInitialHome(){
    let location = this.getUserLocation();

    return this.http.get(`${CONFIG.url}/restaurant/latitude/${location.latitude}/longitude/${location.longitude}/offset/${this.offset}/limit/15`)
      .timeout(20000)
      .map(res => {
        this.haveMore = res.json().data && res.json().data.length ? true : false;
        this.restaurants = this.arrayUnique(this.restaurants.concat(res.json().data));
        return res.json();
      })
      .catch(error => {
        return Observable.throw(error);
      });
  }

  // Melhorar Chamada getInitialHome e getRestaurants fazem a mesma coisa
  getRestaurants( offset ){
    let location = this.getUserLocation();

    return this.http.get(`${CONFIG.url}/restaurant/latitude/${location.latitude}/longitude/${location.longitude}/offset/${offset}/limit/15`)
      .timeout(20000)
      .map(res => {
        if(offset === 0){
          this.restaurants = res.json().data;
        }else{
          this.restaurants = this.arrayUnique(this.restaurants.concat(res.json().data));
        }
        return res.json();
      })
      .catch(error => {
        return Observable.throw(error);
      });
  }

  resetOffset() {
    this.offset = 0;
    this.restaurants = [];
  }

  setOffset(offset) {
    this.offset += offset;
  }

  // Pega a localização do usuário
  getUserLocation() {
    let location = this.loginService.getUser().location;

    return location ? location : {latitude: null, longitude: null};
  }

  getToken() {
    return this.loginService.getToken();
  }

  getHaveMore() {
    return this.haveMore;
  }

  //Busca todos os restaurantes por ids [1,2,3]
  getByIds(ids){

    let lat, lng;
    let location = this.getUserLocation();

    if(location && location.latitude){
      lat = location.latitude;
      lng = location.longitude;
    }

    return this.http.get(CONFIG.url + "/restaurant/ids/"+JSON.stringify(ids)+"/latitude/"+lat+"/longitude/"+lng)
      .timeout(10000)
      .map(res => {
        let restaurants = res.json().data;
        if(restaurants && restaurants.length){
          for (let i = 0; i < restaurants.length; i++) {
            this.favoritesRestaurants.push(restaurants[i]);
          }
        }
        return res.json();
      })
      .catch(error => {
        return Observable.throw(error);
      });
  }

  // Retorna os que ainda não foi carregado
  getByFavoriteAlreadyLoad(ids) {
    this.favoritesRestaurants = [];
    if(!this.restaurants){
      this.restaurants = [];
    }
    for (let i = 0; i < this.restaurants.length; i++) {
      for (let j = 0; j < ids.length; j++) {
        if(this.restaurants[i].id === ids[j] ){
          this.favoritesRestaurants.push(this.restaurants[i]);
          ids.splice(j, 1);
          break;
        }
      }
    }
    return ids;
  }

  getFavoritesRestaurants(){
    return this.favoritesRestaurants;
  }

  // Busca os restaurantes pelo nome %name%
  getByLike(value){
    let location = this.getUserLocation();

    return this.http.get(CONFIG.url + "/restaurant/like/"+value+"/latitude/"+location.latitude+"/longitude/"+location.longitude)
      .map(res => {
        return res.json();
      })
      .catch(error => {
        return Observable.throw(error);
      });
  }

  // Busca os restaurantes pela categoria do restaurante
  getByCategory(id) {
    let location = this.getUserLocation();

    return this.http.get(CONFIG.url + "/restaurant/category/"+id+"/latitude/"+location.latitude+"/longitude/"+location.longitude)
      .timeout(10000)
      .map(res => {
        return res.json();
      })
      .catch(error => {
        return Observable.throw(error);
      });
  }

  getAll() {
    return this.restaurants;
  }

  getPayment(id){
    let validPayments = [];
    for(let i=0; i < this.restaurants.length; i++ ){
      if(this.restaurants[i].id === id){
        return this.restaurants[i].Payments;
      }
    }
    return validPayments;
  }

  // Busca o restaurante pelo id -> busca completa
  getById(id){
    let location = this.getUserLocation();

    return this.http.get(CONFIG.url + "/restaurant/"+id+"/latitude/"+location.latitude+"/longitude/"+location.longitude)
      .timeout(15000)
      .map(res => {
        if(res.json().status){
          let restaurant = this.getExistById(res.json().data.id);
          if(restaurant && restaurant.id){
            this.overwriteRestaurant(res.json().data);
          }else{
            this.restaurants.push(res.json().data);
          }
        }
        return res.json().data;
      })
      .catch(error => {
        return Observable.throw(error);
      });
  }

  // Verifica se o restaurante ja esta na lista carregada
  getExistById(id){
    for(let i=0; i < this.restaurants.length; i++ ){
      if(this.restaurants[i].id === id){
        return this.restaurants[i];
      }
    }
    return {};
  }

  // Sobreescreve o restaurante já carregado
  overwriteRestaurant(restaurant){
    for (let i = 0; i < this.restaurants.length; i++) {
      if (this.restaurants[i].id === restaurant.id) {
        this.restaurants[i] = restaurant;
      }
    }
  }

  remove(item) {
    this.restaurants.splice(this.restaurants.indexOf(item), 1);
  }

  rating(rate) {
    let headers = new Headers();
    headers.append('authorization', this.getToken());
    return this.http.post(CONFIG.url + "/rating", rate, {headers: headers})
      .timeout(10000)
      .map(res => {
        let result = res.json().data;
        let json = {
          id: rate.restaurants_id,
          stars: result
        }
        this.updateRatingRestaurant(json).subscribe(result => {});
        return res.json();
      })
      .catch(error => {
        return Observable.throw(error);
      });
  }

  updateRatingRestaurant(json){
    let headers = new Headers();
    headers.append('authorization', this.getToken());
    return this.http.put(CONFIG.url + "/restaurant/rating", json, {headers: headers})
      .map(res => {
        return res.json();
      })
      .catch(error => {
        return Observable.throw(error);
      });
  }

  getRatings(restaurantId) {
    return this.http.get(CONFIG.url + "/rating/restaurant/"+restaurantId)
      .map(res => {
        return res.json();
      })
      .catch(error => {
        return Observable.throw(error);
      });
  }

  lead( restaurant ) {
    restaurant.createdBy = this.loginService.getUser().email;
    let headers = new Headers();
    headers.append('authorization', this.getToken());
    return this.http.post(CONFIG.url + "/lead", restaurant, {headers: headers})
            .map(res => res.json())
            .catch(error => error.json());
  }

   arrayUnique(array) {
    let a = array.concat();
    for(let i=0; i<a.length; ++i) {
      for(let j=i+1; j<a.length; ++j) {
        if(a[i].id === a[j].id)
          a.splice(j--, 1);
      }
    }

    return a;
  }

}


