import {
	Component
} from '@angular/core';
import {
	AlertController,
	NavController,
	NavParams
} from 'ionic-angular';

import {
	SelectAddressPage
} from './select-address/select-address';
import {
	CartService
} from '../../services/cart-service';
import {
	RestaurantService
} from '../../services/restaurant-service';

import {
	GoogleAnalytics
} from '@ionic-native/google-analytics';

import {
	PaymentPage
} from '../payment/payment';

import { StatusBar } from '@ionic-native/status-bar';
/**
 * Generated class for the DeliveryPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@Component({
	selector: 'page-delivery',
	templateUrl: 'delivery.html',
})
export class DeliveryPage {

	cart: any;
	restaurant: any;

	// Value -> valor para definir se Cliente vai buscar ou vai entregar.
	// Nota: colocar direto o valor this.cart.flag_delivery = "0" é atribuido como false, não selecionando o campo inicialmente.
	public value: string;
	constructor(public nav: NavController,
		public navParams: NavParams,
		private alertCtrl: AlertController,
		public cartService: CartService,
		public ga: GoogleAnalytics,
		private statusBar: StatusBar,
		public restaurantService: RestaurantService) {

		// let status bar overlay webview
	    this.statusBar.overlaysWebView(false);

	    // set status bar to white
	    this.statusBar.backgroundColorByHexString('#ffb42d');

		this.ga.trackView('Tela buscar ou entregar');

		this.cart = this.cartService.getCart();

	}

	ionViewDidLoad() {
		console.log('ionViewDidLoad DeliveryPage');
	}

	ionViewWillEnter() {

		var delivery = this.cart.flag_delivery;
		if (!delivery) {
			if (this.cart.orders.restaurant.delivery_pickup === 0 || this.cart.orders.restaurant.delivery_pickup === 2) {
				this.cart.flag_delivery = '1';
			} else {
				this.cart.flag_delivery = '0';
			}
		}
		this.value = this.cart.flag_delivery;
	}

	checkDelivery() {
		if (this.cart.frete === null) {
			this.cart.flag_delivery = '0';
			this.value = '0';
			return false;
		}
		return true;
	}

	presentConfirm() {
		let alert = this.alertCtrl.create({
			title: 'Você vai buscar?',
			message: 'Confirma que irá buscar o pedido?',
			buttons: [{
				text: 'Não',
				role: 'cancel',
				handler: () => {
					console.log('Cancel clicked');
				}
			}, {
				text: 'Sim',
				handler: () => {
					this.cart.flag_delivery = this.value;
					this.nav.push(PaymentPage);
				}
			}]
		});
		alert.present();
	}

	goPaymentPage() {
		if (this.value === "1" && (!this.cart.orders.address || !this.cart.orders.address.street)) {
			this.presentAlert("Alerta", "Você ainda não possui nenhum endereço cadastrado.");
			return false;
		}

		if (this.value === "0") {
			this.presentConfirm();
		} else {
			this.cart.flag_delivery = this.value;
			this.nav.push(PaymentPage);
		}
	}

	selectAddress() {
		this.nav.push(SelectAddressPage);
	}

	noDelivery() {
		console.log("Vai Buscar");
		this.cart.flag_delivery = '0';
	}

	yesDelivery() {
		console.log("Entrega");
		this.cart.flag_delivery = '1';
	}

	// go to the payment page
	formPayment() {
		this.nav.push(PaymentPage);
	}

	getRestaurantDelivery() {

		// 0 Entregar, 1 Buscar, 2 Entregar/Buscar
		if (this.cart.orders.restaurant.delivery_pickup === 1) {
			return false;
		}

		return this.cart.frete === null ? false : true;
	}

	getFrete(frete) {
		if (frete === null) return '';

		if (isNaN(frete)) {
			return frete;
		} else {
			return "R$ " + frete.toFixed(2);
		}
	}

	presentAlert(title, subTitle) {
		let alert = this.alertCtrl.create({
			title: title,
			subTitle: subTitle,
			buttons: ['Ok']
		});
		alert.present();
	}

	getTotal() {
		return this.cartService.getTotal().toFixed(2);
	}

}


