import {Component} from '@angular/core';
import {App, LoadingController, NavController, ToastController} from 'ionic-angular';

import {RestaurantPage} from "../restaurant/restaurant";
import {CategoryService} from '../../services/category-service';
import {RestaurantService} from "../../services/restaurant-service";

import { 
  LocationPage
} from '../location/location';

import {
  LoginService
} from "../../services/login-service";

import {
  CartPage
} from "../cart/cart";

import {
  CartService
} from '../../services/cart-service';

import {
  WithoutInternetPage
} from "../without-internet/without-internet";

import {
  CheckInternet
} from '../../services/check-internet';

import {
  LocationAccuracy
} from '@ionic-native/location-accuracy';

import {
  Geolocation
} from '@ionic-native/geolocation';

import {
  Http
} from '@angular/http';

import {
  LoginPage
} from "../login/login";

import {
  SmsPage
} from '../sms/sms';

import {UsersProvider } from '../../providers/users/users';

import { GoogleAnalytics } from '@ionic-native/google-analytics';

import { StatusBar } from '@ionic-native/status-bar';
/*
  Generated class for the LoginPage page.

  See http://ionicframework.com/docs/v2/components/#navigation for more info on
  Ionic pages and navigation.
*/
@Component({
  selector: 'page-search',
  templateUrl: 'search.html',
})
export class SearchPage {
  categories: any;
  keyword = null;
  showResults = false;
  restaurants = [];
  message:string;
  location:any;
  showSpinner:boolean = false;
  isLogged:boolean = false;
  user:any;

  //Cliente selecionou algum item?
  hasItems: boolean;

  constructor(public nav: NavController,
              public app: App,
              private loadingCtrl: LoadingController,
              private toastCtrl: ToastController,
              public cartService: CartService,
              public categoryService: CategoryService,
              private loginService: LoginService,
              private checkInternet: CheckInternet,
              private locationAccuracy: LocationAccuracy,
              private geolocation: Geolocation,
              public ga: GoogleAnalytics,
              private statusBar: StatusBar,
              public http: Http,
              private usersProvider: UsersProvider,
              public restaurantService: RestaurantService) {

    // let status bar overlay webview
    this.statusBar.overlaysWebView(false);

    // set status bar to white
    this.statusBar.backgroundColorByHexString('#ffb42d');

    this.ga.trackView('Tela de Busca');
  }

  login() {
    this.nav.push(LoginPage);
  }

  findCategories() {
    // Antes de tudo vericar se usuário possui Internet
    if (!this.checkInternet.isInternet()) {
      this.withoutInternet();
      return false;
    }

    let loader = this.loadingCtrl.create({
        content: 'Carregando...'
      });
    this.showSpinner = true;
    // Show the popup
    loader.present();
    loader.dismiss().then(() => {
        this.categoryService.populate().subscribe(result => {
            this.categories = result;
            this.showSpinner = false;
        }, error => {
          this.showSpinner = false;
          if(error.status === 401){
            this.presentToast("Sua sessão expirou");
            this.goToLogout();
          }
        });
      }, error => {
        console.log(error);
        this.showSpinner = false;
        this.presentToast("Desculpe. Tente novamente.");
        if(error.status === 401){
          this.presentToast("Sua sessão expirou");
          this.goToLogout();
        }
    });
  }

  // filter list categories
  filterCat(ev: any) {
    this.categories = this.categoryService.getAll();

    // set val to the value of the searchbar
    let val = ev.target.value;

    // if the value is an empty string don't filter the items
    if (val && val.trim() != '') {
      this.categories = this.categories.filter((item) => {
        return (item.name.toLowerCase().indexOf(val.toLowerCase()) > -1);
      })
      if(!this.categories.length){
        this.showResults = true;
        this.findByLike(val);
      }else{
        this.showResults = false;
      }
    }
  }

  findByLike( value ){
    this.showSpinner = true;
    this.restaurantService.getByLike(value).subscribe(data => {
        if(data.status){
          this.restaurants = data.data
          this.showResults = true;
          this.message = data.message;
          this.showSpinner = false;
        }else{
          this.showResults = true;
          this.showSpinner = false;
        }
     }, error => {
        if(error.status === 401){
          this.presentToast("Sua sessão expirou");
          this.goToLogout();
        }
     });
  }

  // clear filter
  clearFilter(ev: any) {
    this.categories = this.categoryService.getAll();
    this.showResults = false;
    this.keyword = null;
  }

  // search by keyword
  search(keyword) {
    this.keyword = keyword.name;
    this.findByCategory(keyword.id);
  }

  findByCategory(keyword) {
    // Antes de tudo vericar se usuário possui Internet
    if (!this.checkInternet.isInternet()) {
      this.withoutInternet();
      return false;
    }

    this.showSpinner = true;
      this.restaurantService.getByCategory(keyword).subscribe(data => {
            // remove the popup
            if(data.status){
              this.restaurants = data.data
              this.showResults = true;
            }else{
              this.showResults = true;
            }
            this.showSpinner = false;
         }, error => {
            // remove the popup
            this.showSpinner = false;
            if(error.status === 401){
              this.presentToast("Sua sessão expirou");
              this.goToLogout();
            }
         });
  }

  // view restaurant detail
  viewRestaurant(restaurant) {
    let id = restaurant.id;

    // Antes de tudo vericar se usuário possui Internet
    if (!this.checkInternet.isInternet()) {
      this.withoutInternet();
      return false;
    }

    restaurant = this.restaurantService.getExistById(id);
    //Caso já tenha carregado o restaurante pelo Id não é necessário carregar novamente
    if(restaurant && restaurant.Cuisines){
      this.app.getRootNav().push(RestaurantPage, {
            id: id
          })
    }else{
      let loader = this.loadingCtrl.create({
          content: 'Carregando...'
        });
      // Show the popup
      loader.present();
      loader.present().then(() => {
        this.restaurantService.getById(id).subscribe(data => {
          loader.dismiss();
          this.app.getRootNav().push(RestaurantPage, {
            id: id
          })
        }, error => {
          console.log(error);
          loader.dismiss();
          this.presentToast("Desculpe. Tente novamente.");
          if(error.status === 401){
            this.presentToast("Sua sessão expirou");
            this.goToLogout();
          }
        });
      });
    }
  }

  withoutInternet() {
    this.app.getRootNav().push(WithoutInternetPage);
  }

  ionViewDidEnter() {

    // Antes de tudo vericar se usuário possui Internet
    if (!this.checkInternet.isInternet()) {
      this.withoutInternet();
      return false;
    }

    this.isLogged = this.loginService.isLogged();
    this.user = this.loginService.getUser();

    this.location = {};
    this.hasItems = false;
    this.categories = this.categoryService.getAll();
    if(!this.categories || !this.categories.length){
      this.findCategories();
    }

    this.location = this.loginService.getUser().location;

    // Verifica se possui itens no Carrinho
    var cart = this.cartService.getCart();
    if (cart && cart.orders && cart.orders.restaurant && cart.orders.restaurant.id) {
      this.hasItems = cart.orders.items.length ? true : false;
    } else {
      this.hasItems = false;
    }

  }


  changeAddress() {
    this.getAddressByGoogleMaps();
  }

  getAddressByGoogleMaps() {
    this.nav.push(LocationPage);
    this.showResults = false;
  }

  // go to cart page
  goToCart() {
    this.nav.push(CartPage);
  }

  presentToast(message) {
    let toast = this.toastCtrl.create({
      message: message,
      duration: 3000,
      position: 'top'
    });

    toast.present();
  }

  getFrete(restaurant) {

    let frete = this.cartService.calculateFrete(restaurant.frete,
      restaurant.frete_km, restaurant.km_max,
      restaurant.km_normal, restaurant.distance);

    if(typeof frete === 'string'){
      return true;
    }
    return false;
  }

  getCircle(index, size){
    return index+1 < size ? '• ' : '';
  }

    //Utiliza o plugin locationAccurancy para pegar a posição do usuário pelo GPS do celular.
  closeToMe() {
    // Antes de tudo vericar se usuário possui Internet
    if (!this.checkInternet.isInternet()) {
      this.presentToast("Sem acesso a internet");
      return false;
    }

    let loader = this.loadingCtrl.create({
      content: 'Carregando...'
    });

    // Show the popup
    loader.present();
    loader.dismiss().then(() => {

      // the accuracy option will be ignored by iOS
      this.locationAccuracy.request(this.locationAccuracy.REQUEST_PRIORITY_HIGH_ACCURACY).then(
        () => {
          this.location = {};
          this.geolocation.getCurrentPosition().then((resp) => {
            this.location.latitude = resp.coords.latitude;
            this.location.longitude = resp.coords.longitude;
            if (this.location.latitude && this.location.longitude) {
              this.getAddress(this.location.latitude, this.location.longitude);
            }
          }).catch((error) => {
            console.log('Error getting location', error);
            this.presentToast('Desculpe. Não conseguimos a sua localização. Tente novamente!');
          });
        },
        error => {
          console.log('Error requesting location permissions', error);
          this.presentToast('Desculpe. Não conseguimos a sua localização. Tente novamente!');
        }
      );
    });

  }

  // Busca o endereço pela Latitude e Longitude
  getAddress(lat, lng) {
    // Antes de tudo vericar se usuário possui Internet
    if (!this.checkInternet.isInternet()) {
      this.presentToast("Sem acesso a internet");
      return false;
    }

    let loader = this.loadingCtrl.create({
      content: 'Carregando...'
    });

    // Show the popup
    loader.present();
    loader.dismiss().then(() => {
      this.getLocationByLatAndLng(lat, lng, loader).subscribe(result => {
        // Seta o endereço no carrinho de compras
        //this.setAddress(address);
      }, error => {
        console.log(error);
      });
    });
  }

  // Busca o endereço na API do Google Maps pela latitude e longitude
  getLocationByLatAndLng(lat, lng, loader) {

    let env = this;
    return this.http.get("http://maps.googleapis.com/maps/api/geocode/json?latlng=" + lat + "," + lng + "&sensor=true")
      .map(res => {
        let formatJson = JSON.parse(res["_body"]);

        // Convert resposta da API do google em um objeto
        let address = env.loginService.mountAddress(formatJson.results[0].address_components, env.location);

        // Seta o endereço recebido da API
        env.loginService.setLocation(address);

        loader.dismiss();
        env.location = address;
        return res;
      })
      .catch(error => {
        loader.dismiss();
        return error;
      });
  }

  fullActivation() {
    let user = this.loginService.getUser();
    if(user && user.activation_code === false){
      this.sms();
    }
  }

  // Tela de sms, valida número telefonico
  sms() {
    this.app.getRootNav().push(SmsPage);
  }

  // go to login page
  goToLogout() {
    this.updateUser();
      this.app.getRootNav().setRoot(LoginPage);
  }
  // Remove o usuário do banco de dados local
  private updateUser() {
      return this.usersProvider.update({id: 1, json: '{}'});
  }

}



