import {Component} from '@angular/core';
import {AlertController, NavController, LoadingController, ToastController} from 'ionic-angular';

import {FormBuilder, Validators} from '@angular/forms';

import {UserService} from '../../services/user-service';

import {
  CheckInternet
} from '../../services/check-internet';

import { StatusBar } from '@ionic-native/status-bar';
/*
  Generated class for the LoginPage page.

  See http://ionicframework.com/docs/v2/components/#navigation for more info on
  Ionic pages and navigation.
*/
@Component({
  selector: 'page-recover',
  templateUrl: 'recover.html',
})
export class RecoverPage {
 
 	recover:any;
	constructor(public nav: NavController,
	  			public formBuilder: FormBuilder,
	  			public userService: UserService,
	  			private toastCtrl: ToastController,
	  			private alertCtrl: AlertController,
	  			private checkInternet: CheckInternet,
	  			private statusBar: StatusBar,
	  			private loadingCtrl: LoadingController) {

		// let status bar overlay webview
		this.statusBar.overlaysWebView(false);

		// set status bar to white
		this.statusBar.backgroundColorByHexString('#ffb42d');

	  	this.recover = this.formBuilder.group({
	      email:['', Validators.required]
	    });
	}

	recoverPassword(email) {
		// Antes de tudo vericar se usuário possui Internet
	    if (!this.checkInternet.isInternet()) {
	      this.presentToast("Sem acesso a internet");
	      return false;
	    }
    
		// Create the popup
	    let loader = this.loadingCtrl.create({
	      content: 'Enviando nova senha...'
	    });
	    // Show the popup
	    loader.present();
	    loader.present().then(() => {
	      this.userService.recoverPassword(email).subscribe(data => {
	            // remove the popup
	            loader.dismiss();
	            if(data.status){	              
	              this.presentToast(data.message);
	            }else{
	             this.presentAlert(data.message) 
	            }
	         }, error => {
	             console.log(error);
	             // remove the popup
	            loader.dismiss();
	         });
	    });
	}

	presentAlert(subTitle) {
	    let alert = this.alertCtrl.create({
	      title: 'Alerta',
	      subTitle: subTitle,
	      buttons: ['Ok']
	    });
	    alert.present();
	}

	presentToast(message) {
	    let toast = this.toastCtrl.create({
	      message: message,
	      duration: 4000,
	      position: 'top'
	    });

	    toast.present();
    }

}


