import {
  Component
} from '@angular/core';
import {
  App,
  AlertController,
  LoadingController,
  NavController,
  ToastController
} from 'ionic-angular';

import {
  RestaurantService
} from '../../services/restaurant-service';
import {
  RestaurantPage
} from "../restaurant/restaurant";

import { 
  LeadPage
} from "../lead/lead";

import {
  LoginService
} from "../../services/login-service";

import {
  LocationPage
} from '../location/location';

import {
  HistoryOrderService
} from '../../services/history-order-service';

import {
  RatingPage
} from '../rating/rating';

import {
  SmsPage
} from '../sms/sms';

import {
  WithoutInternetPage
} from "../without-internet/without-internet";

import {
  CheckInternet
} from '../../services/check-internet';

import {
  LocationAccuracy
} from '@ionic-native/location-accuracy';

import {
  Geolocation
} from '@ionic-native/geolocation';

import {
  Http
} from '@angular/http';

import {
  LoginPage
} from "../login/login"; 

import {
  CitiesPage
} from "../cities/cities"; 

import {UsersProvider } from '../../providers/users/users';

import {
  CartService
} from '../../services/cart-service';

import { GoogleAnalytics } from '@ionic-native/google-analytics';

import { StatusBar } from '@ionic-native/status-bar';

/*
  Generated class for the LoginPage page.

  See http://ionicframework.com/docs/v2/components/#navigation for more info on
  Ionic pages and navigation.
*/
@Component({
  selector: 'page-home',
  templateUrl: 'home.html',
})
export class HomePage {

  restaurants: any;
  message: string;
  location: any;
  showSpinner: boolean;
  offset: number;
  haveMore:boolean;
  isLogged:boolean = false;
  user:any;

  constructor(public app: App,
    public nav: NavController,
    private loadingCtrl: LoadingController,
    private alertCtrl: AlertController,
    private toastCtrl: ToastController,
    private restaurantService: RestaurantService,
    public historyOrderService: HistoryOrderService,
    private checkInternet: CheckInternet,
    private locationAccuracy: LocationAccuracy,
    private geolocation: Geolocation,
    public http: Http,
    private statusBar: StatusBar, 
    public ga: GoogleAnalytics,
    private usersProvider: UsersProvider,
    public cartService: CartService,
    private loginService: LoginService) {

    // let status bar overlay webview
    this.statusBar.overlaysWebView(false);

    // set status bar to white
    this.statusBar.backgroundColorByHexString('#ffb42d');
    
    this.haveMore = true;
    this.ga.trackView('Tela Principal');

    this.location = {};
    this.showSpinner = false;
    this.getUserById();
  }

  login() {
    this.nav.push(LoginPage);
  }

  // view restaurant detail
  viewRestaurant(restaurant) {
    let id = restaurant.id;

    restaurant = this.restaurantService.getExistById(id);
    //Caso já tenha carregado o restaurante pelo Id não é necessário carregar novamente
    if (restaurant && restaurant.Cuisines) {
      this.app.getRootNav().push(RestaurantPage, {
        id: id
      })
    } else {
      if (!this.checkInternet.isInternet()) {
        this.withoutInternet();
        return false;
      }
      let loader = this.loadingCtrl.create({
        content: 'Carregando...'
      });
      // Show the popup
      loader.present().then(() => {
        this.restaurantService.getById(id).subscribe(data => {
          loader.dismiss();
          this.app.getRootNav().push(RestaurantPage, {
            id: id
          })
        }, error => {
          loader.dismiss();
          if(error.name === "TimeoutError"){
            this.presentToast('Tente novamente!');
          }
          if(error.status === 401){
            this.presentToast("Sua sessão expirou");
            this.goToLogout();
          }
          console.log(error);
        });
      }, error => {
        loader.dismiss();
        this.presentToast("Desculpe. Tente novamente.");
      });
    }
  }

  // Busca os restaurentes na base de dados
  findRestaurant() {
    // Antes de tudo vericar se usuário possui Internet
    if (!this.checkInternet.isInternet()) {
      this.withoutInternet();
      return false;
    }
    let env = this;

    this.showSpinner = true;

    this.location = this.restaurantService.getUserLocation();
    if (this.location && this.location.latitude && this.location.longitude) {
      this.restaurantService.getInitialHome().subscribe(result => {
        setTimeout(() => {
            env.restaurants = this.restaurantService.getAll();
        }, 0);
        env.message = result.message;
        env.showSpinner = false;
      }, error => {
        console.log(error);
        env.showSpinner = false;
        if(error.status === 401){
          env.presentToast("Sua sessão expirou");
          env.goToLogout();
        }
      });
    }
  }

  // Chama Tela para selecionar endereço.
  getMyAddresses() {
    this.nav.push(LocationPage);
  }

  // Trocar de endereço
  changeAddress() {
    this.getMyAddresses();
  }

  // Tela de sem acesso a internet
  withoutInternet() {
    this.app.getRootNav().push(WithoutInternetPage);
  }

  fullActivation() {
    let user = this.loginService.getUser();
    if(user && (user.activation_code === false)){
      this.sms();
    }
  }
  // Tela de sms, valida número telefonico
  sms() {
    this.app.getRootNav().push(SmsPage);
  }

  //Verifica se possui algum pedido que precisa ser avaliado
  findHistoryOrder() {

    if(this.isLogged){
      // Antes de tudo vericar se usuário possui Internet
      if (!this.checkInternet.isInternet()) {
        this.withoutInternet();
        return false;
      }

      this.historyOrderService.getByUser().subscribe(data => {
        // remove the popup
        let orders = data.data;
        if(orders){
          for (let i = 0; i < orders.length; i++) {
            if (orders[i].status === "4" && !orders[i].orders.rating) {
              this.getRating(orders[i]);
              break;
            }
          }
        }
      }, error => {
        console.log(error);
      });
    }
  }

  // Tela para avaliação do restaurante
  getRating(order) {
    this.nav.push(RatingPage, {
      order: JSON.stringify(order)
    });
  }

  ionViewDidEnter() {
    console.log("Entrei enter home");
    this.restaurantService.resetOffset();
    // Antes de tudo vericar se usuário possui Internet
    if (!this.checkInternet.isInternet()) {
      this.withoutInternet();
      return false;
    }

    this.isLogged = this.loginService.isLogged();
    this.user = this.loginService.getUser();

    this.findRestaurant();
    this.findHistoryOrder();

    // Busca todos os restaurantes
    this.restaurants = this.restaurantService.getAll();
    // Caso ainda não tenha restaurante é realizado uma nova busca
    if (!this.restaurants || !this.restaurants.length) {
      this.message = 'Nenhum estabelecimento foi encontrado';
    } else {
      this.showSpinner = false;
    }

  }

  getUserName() {
    if(this.isLogged){
      let user = this.loginService.getUser();
      return  user.first_name;
    }
  }

  presentToast(message) {
    let toast = this.toastCtrl.create({
      message: message,
      duration: 3000,
      position: 'top'
    });

    toast.present();
  }

  getCircle(index, size) {
    return index + 1 < size ? '• ' : '';
  }

  goToCities(){
    this.nav.push(CitiesPage);
  }

  presentAlert(title,subTitle) {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: subTitle,
      buttons: ['Ok']
    });
    alert.present();
  }

  getShowSpinner() {
    if(!this.location || !this.location.street ) {
      return false;
    }
    return this.showSpinner;
  }

  //Utiliza o plugin locationAccurancy para pegar a posição do usuário pelo GPS do celular.
  closeToMe() {
  
    // Antes de tudo vericar se usuário possui Internet
    if (!this.checkInternet.isInternet()) {
      this.presentToast("Sem acesso a internet");
      this.withoutInternet();
      return false;
    }
    
    let loader = this.loadingCtrl.create({
      content: 'Carregando...'
    });

    // Show the popup
    loader.present().then(() => {

      // the accuracy option will be ignored by iOS
      this.locationAccuracy.request(this.locationAccuracy.REQUEST_PRIORITY_HIGH_ACCURACY).then(
        () => {
          this.location = {};
          this.geolocation.getCurrentPosition().then((resp) => {
            loader.dismiss();
            this.location.latitude = resp.coords.latitude;
            this.location.longitude = resp.coords.longitude;
            if (this.location.latitude && this.location.longitude) {
              this.getAddress(this.location.latitude, this.location.longitude);
            }
          }).catch((error) => {
            loader.dismiss();
            console.log('Error getting location', error);
            this.presentToast('Desculpe. Não conseguimos a sua localização. Tente novamente!');
          });
        },
        error => {
          loader.dismiss();
          console.log('Error requesting location permissions', error);
          this.presentToast('Desculpe. Não conseguimos a sua localização. Tente novamente!');
        }
      );
    });

  }

  // Busca o endereço pela Latitude e Longitude
  getAddress(lat, lng) {
    // Antes de tudo vericar se usuário possui Internet
    if (!this.checkInternet.isInternet()) {
      this.presentToast("Sem acesso a internet");
      this.withoutInternet();
      return false;
    }

    let loader = this.loadingCtrl.create({
      content: 'Carregando...'
    });

    // Show the popup
    loader.present();
    loader.dismiss().then(() => {
      this.getLocationByLatAndLng(lat, lng, loader).subscribe(result => {
        // Seta o endereço no carrinho de compras
        //this.setAddress(address);
      }, error => {
        console.log(error);
        loader.dismiss();
      });
    });
  }

  // Busca o endereço na API do Google Maps pela latitude e longitude
  getLocationByLatAndLng(lat, lng, loader) {

    let env = this;
    return this.http.get("https://maps.googleapis.com/maps/api/geocode/json?latlng=" + lat + "," + lng + "&sensor=true&key=AIzaSyAnIl-LlxxlgkQ5iD9MiHdEj4AJ-g9K8nc")
      .map(res => {
        loader.dismiss();
        let formatJson = JSON.parse(res["_body"]);

        // Convert resposta da API do google em um objeto
        if(formatJson.results.length){
          let address = env.loginService.mountAddress(formatJson.results[0].address_components, env.location);

          // Seta o endereço recebido da API
          env.loginService.setLocation(address);

          env.findRestaurant();
        }else{
          env.presentToast("Desculpe, não conseguimos a sua localização. Tente novamente.");
        }
        return res;
      })
      .catch(error => {
        loader.dismiss();
        return error;
      });
  }

  // go to login page
  goToLogout() {
    this.updateUser();
    this.nav.push(LoginPage);
  }

  goToLead() {
    this.app.getRootNav().push(LeadPage);
  }

  // Remove o usuário do banco de dados local
  private updateUser() {
      return this.usersProvider.update({id: 1, json: '{}'});
  }

  getFrete(restaurant) {

    let frete = this.cartService.calculateFrete(restaurant.frete,
      restaurant.frete_km, restaurant.km_max,
      restaurant.km_normal, restaurant.distance);

    return typeof frete === 'string';

  }

  doRefresh(refresher) {

    let env = this;
    this.restaurantService.getRestaurants(0).subscribe(result => {
      setTimeout(() => {
        env.restaurants = this.restaurantService.getAll();
      }, 0);
      env.message = result.message;
      refresher.complete();
    }, error => {
      console.log(error);
      refresher.complete();
      if(error.name === "TimeoutError"){
        this.presentToast('Tente novamente!');
      }
      if(error.status === 401){
        env.presentToast("Sua sessão expirou");
        env.goToLogout();
      }
    });
  }

  doInfinite(infiniteScroll) {
    this.haveMore = this.restaurantService.getHaveMore();
    if(this.haveMore){
      this.restaurantService.setOffset(1);
      this.restaurantService.getInitialHome().subscribe(result => {
        this.restaurants = this.restaurantService.getAll();
        this.message = result.message;
        infiniteScroll.complete();
      }, error => {
        console.log(error);
        infiniteScroll.complete();
        if(error.status === 401){
          this.presentToast("Sua sessão expirou");
          this.goToLogout();
        }
      });
    }
  }

  //Busca usuário por id. Nesse caso pelo id 1
  getUserById() {
    let id = 1;
    this.usersProvider.getUserById(id)
      .then((result: any) => {
        let user = {
          id: null,
          location: null
        };
        console.log(result);
        if(result){
          user = JSON.parse(result.json);
        }
        console.log("user");
        console.log(user);
        if(user && user.id){

          // Seta o Usuário no LoginService
          this.loginService.setUser(user);
          
          this.isLogged = this.loginService.isLogged();
          this.user = this.loginService.getUser();
        }
        console.log("Banco de dados local");
        this.loginService.setLocation(user.location);
        console.log(this.user);
        if(this.user.location){
          console.log("ENtrei busca restaurantes");
          this.findRestaurant();
        }
      });
  }

}



