import {Component} from '@angular/core';
import {NavController, LoadingController} from 'ionic-angular';

import {NewAddressPage} from '../../address/new-address/new-address';
import {LoginService} from '../../../services/login-service';
import {AddressService} from '../../../services/address-service';
import {CartService} from '../../../services/cart-service';

import { StatusBar } from '@ionic-native/status-bar';
/*
  Generated class for the LoginPage page.

  See http://ionicframework.com/docs/v2/components/#navigation for more info on
  Ionic pages and navigation.
*/
@Component({
  selector: 'page-select-address',
  templateUrl: 'select-address.html',
})
export class SelectAddressPage {

 	  address:any;
  	constructor(public nav: NavController,
  				      public addressService: AddressService,
                public cartService: CartService,
                private statusBar: StatusBar,
        				public loginService: LoginService,
                private loadingCtrl: LoadingController) {

      // let status bar overlay webview
      this.statusBar.overlaysWebView(false);

      // set status bar to white
      this.statusBar.backgroundColorByHexString('#ffb42d');
      
      this.address = this.addressService.getAll();
      if(!this.address.length){
        this.findAddress();  
      }
  		
  	}

    findAddress(){
      // Create the popup
      let loader = this.loadingCtrl.create({
        content: 'Buscando endereços...'
      });
      // Show the popup
      loader.present();
      loader.present().then(() => {
        this.addressService.findById(this.loginService.getUser().id).subscribe(data => {
              // remove the popup
              loader.dismiss();
              this.address = data.data;
           }, error => {
               console.log(error);
               // remove the popup
              loader.dismiss();
           });
      });
    }

    calculateFrete() {
      // Create the popup
      let loader = this.loadingCtrl.create({
        content: 'Calculando frete...'
      });
      // Show the popup
      loader.present();
      loader.present().then(() => {
        
        this.cartService.precoFrete().subscribe(result => {
          loader.dismiss();
          this.cartService.setFrete(result.data.price);
          this.nav.pop();
        }, error => {
             console.log(error);
             // remove the popup
            loader.dismiss();
         });
      });
    }

  	newAddress(){
  		this.nav.push(NewAddressPage, {userId: this.loginService.getUser().id});
  	}

  	changAddress(address) {
      this.cartService.setAddress(address);
      this.calculateFrete();
  	}

    ionViewWillEnter() {
      this.address = this.addressService.getAll();
    }

    
}


