import {
	Component
} from '@angular/core';

import {
	AlertController,
	NavController,
	NavParams,
	LoadingController,
	ToastController
} from 'ionic-angular';

import {
	CartService
} from '../../services/cart-service';

import {
	FinishOrderSuccessPage
} from "../finish-order-success/finish-order-success";

import {
	CouponService
} from "../../services/coupon.service";

import {
	CheckInternet
} from '../../services/check-internet';

import {
  LoginPage
} from "../login/login";

import {
  UsersProvider
} from '../../providers/users/users';

import {
  LoginService
} from "../../services/login-service";

import { GoogleAnalytics } from '@ionic-native/google-analytics';

import { StatusBar } from '@ionic-native/status-bar';

import {
  UserService
} from '../../services/user-service';
/**
 * Generated class for the FinishOrderPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@Component({
	selector: 'page-finish-order',
	templateUrl: 'finish-order.html',
})
export class FinishOrderPage {
	cart: any;
	couponName: string = "";
	aplicarDiscount: any;
	constructor(public nav: NavController,
		public navParams: NavParams,
		private couponService: CouponService,
		private cartService: CartService,
		private statusBar: StatusBar,
		private loadingCtrl: LoadingController,
		private toastCtrl: ToastController,
		private checkInternet: CheckInternet,
		private usersProvider: UsersProvider,
		private loginService: LoginService,
		public ga: GoogleAnalytics,
		public userService: UserService,
		private alertCtrl: AlertController) {

		// let status bar overlay webview
	    this.statusBar.overlaysWebView(false);

	    // set status bar to white
	    this.statusBar.backgroundColorByHexString('#ffb42d');

		this.ga.trackView('Finish Page');

		this.aplicarDiscount = {
			status: false,
			data: {},
			msg: ''
		}
		this.cart = cartService.getCart();
		if (this.cart.discount && this.cart.discount.name) {
			//Calcula do desconto
			this.calculateDiscount();
		}
	}

	ionViewDidLoad() {
		console.log('ionViewDidLoad FinishOrderPage');
	}

	getTotal() {
	    return this.cartService.getTotal().toFixed(2);
	}

	isDelivery() {
		if (parseInt(this.cart.flag_delivery) > 0) {
			return true;
		}
		return false;
	}

	sendOrder() {
		// Antes de tudo vericar se usuário possui Internet
		if (!this.checkInternet.isInternet()) {
			this.presentAlert("Sem acesso a internet");
			return false;
		}

		if (!this.cart.orders.user.phone) {
			this.presentPhone();
			return;
		}

		this.ga.trackEvent('Click', 'Finalizar Pedido', null, null);

		// Create the popup
		let loader = this.loadingCtrl.create({
			content: 'Realizando pedido...'
		});
		// Show the popup
		loader.present();
		loader.present().then(() => {
			this.cartService.sendOrder().subscribe(result => {
				// remove the popup
				loader.dismiss();
				if (result.status) {
					this.loginService.setNewUser(false);
					this.cartService.initCart();
					this.nav.setRoot(FinishOrderSuccessPage, {
						id: result.data.id,
						delivery_min: this.cart.orders.restaurant.delivery_min,
						delivery_max: this.cart.orders.restaurant.delivery_max
					});
				} else {
					this.presentToast(result.message, 6000);
				}
			}, error => {
				if (error.status === 401) {
		          this.presentToast("Sua sessão expirou", 4000);
		          this.goToLogout();
		        }

				if(error.name === "TimeoutError"){
		          this.presentToast('Tente novamente!', 4000);
		        }
				// remove the popup
				loader.dismiss();
			});
		});
 
	}

	optional(item) {
		var array = [];
		for (var i = 0; i < item.options.optional.length; i++) {
			if (item.options.optional[i].selected) {
				array.push(item.options.optional[i]);
			}
		}
		return array;
	}

	presentAlert(subTitle) {
		let alert = this.alertCtrl.create({
			title: 'Alerta',
			subTitle: subTitle,
			buttons: ['Ok']
		});
		alert.present();
	}

	presentToast(message, time) {
		let toast = this.toastCtrl.create({
			message: message,
			duration: time,
			position: 'top'
		});

		toast.present();
	}

	validateCoupon() {
		// Antes de tudo vericar se usuário possui Internet
		if (!this.checkInternet.isInternet()) {
			this.presentAlert("Sem acesso a internet");
			return false;
		}

		if (this.couponName && this.couponName.length > 0) {
			this.couponService.validate(this.cart.restaurants_id, this.couponName).subscribe(result => {
				this.aplicarDiscount = result;
				this.couponName = '';
				if (result.status) {
					this.presentToast(result.msg, 3000);
					if (this.cart.subTotal < 1) {
						this.cart.subTotal = this.cart.total;
					}
					this.cart.discount = {
						id: result.data.id,
						status: result.status,
						name: result.data.name,
						value: result.data.price,
						discount_type: result.data.discount_type
					};

					//Calcula do desconto
					this.calculateDiscount();

				} else {
					this.presentToast(result.message, 3000);
					this.cart.discount = {
						status: result.status
					}
				}
			})
		} else {
			this.presentToast('Deve inserir cupom', 3000);
		}
	}

	presentConfirm(phone) {
		let alert = this.alertCtrl.create({
			title: 'Salvar número telefônico?',
			message: 'Deseja salvar o número telefônico para compras futuras?',
			buttons: [{
				text: 'Não',
				role: 'cancel',
				handler: () => {
					this.sendOrder();
				}
			}, {
				text: 'Sim',
				handler: () => {
					this.updatePhone(phone);
					this.sendOrder();
				}
			}]
		});
		alert.present();
	}

	updatePhone(phone) {
		this.userService.updatePhone(phone).subscribe(result =>{});
	}

	// Adicionar telefone do usuário
	presentPhone() {
		const alert = this.alertCtrl.create({
			title: 'Telefone',
			message: 'Telefone para Contato?',
			inputs: [{
				name: 'phone',
				type: 'number',
				placeholder: ''
			}],
			buttons: [{
				text: 'Pronto',
				handler: data => {
					this.cartService.setPhone(data.phone);
					if (data.phone) {
						this.presentConfirm(data.phone);
					}
				}
			}]
		});
		alert.present();
	}

	nameItemRequired(item) {
		for (var i = 0; i < item.options.required.length; i++) {
			if (item.options.required[i].id === parseInt(item.itemRequired)) {
				return item.options.required[i].name + " • R$ " + item.options.required[i].price;
			}
		}
	}

	getFormatPayment() {
		if (this.cart.orders.payment.name === 'Dinheiro') {
			return this.cart.orders.payment.name + " • Troco: " + this.cart.orders.money;
		} else {
			var online = this.cart.orders.payment.is_online === 0 ? 'máquina' : '';
			return this.cart.orders.payment.card + ' (' + online + ')';
		}
	}

	getDiscount(discount) {
		if (discount.discount_type === "1" || discount.discount_type === true) {
			return discount.value + "%";
		} else {
			return "R$ " + discount.value;
		}
	}

	// Calcula o desconto do cupom inserido
	calculateDiscount() {
		// Se for porcentagem (true)
		if (this.cart.discount.discount_type === "1") {
			this.cart.total = this.cart.subTotal - (this.cart.subTotal * (this.cart.discount.value / 100));
		} else {
			this.cart.total = this.cart.subTotal - this.cart.discount.value;
		}
	}

	// go to login page
	goToLogout() {
		this.updateUser();
		this.nav.push(LoginPage);
	}
	// Remove o usuário do banco de dados local
	private updateUser() {
		return this.usersProvider.update({
			id: 1,
			json: '{}'
		});
	}

	getFrete( frete ) {
	    if(isNaN(frete)){
	      return frete;
	    }else{
	      return "R$ " + frete.toFixed(2);
	    }
	}



}


