import {
  Component
} from '@angular/core';
import {
  NavController,
  NavParams,
  LoadingController,
  ToastController,
  ViewController
} from 'ionic-angular';
import {
  RegisterPage
} from "../register/register";
import {
  RecoverPage
} from "../recover/recover";

import {
  LoginService
} from '../../services/login-service';
import {
  AlertController
} from 'ionic-angular';

import {
  WithoutInternetPage
} from "../without-internet/without-internet";


import {
  CheckInternet
} from '../../services/check-internet';

import {
  Facebook
} from '@ionic-native/facebook';

import {
  UsersProvider,
  User
} from '../../providers/users/users';

import { GoogleAnalytics } from '@ionic-native/google-analytics';

import { StatusBar } from '@ionic-native/status-bar';
/*
  Generated class for the LoginPage page.

  See http://ionicframework.com/docs/v2/components/#navigation for more info on
  Ionic pages and navigation.
*/
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  profile: any;

  constructor(public nav: NavController,
    private view: ViewController,
    public navParams: NavParams,
    private loginService: LoginService,
    private alertCtrl: AlertController,
    private toastCtrl: ToastController,
    private facebook: Facebook,
    private ga: GoogleAnalytics,
    private statusBar: StatusBar,
    private checkInternet: CheckInternet,
    private loadingCtrl: LoadingController,
    private usersProvider: UsersProvider) {

    // let status bar overlay webview
    this.statusBar.overlaysWebView(false);

    // set status bar to white
    this.statusBar.backgroundColorByHexString('#ffb42d');

    this.ga.trackView('Tela Login');

    this.loginService = loginService;
    this.profile = {};

    // this.profile.email = 'sms@gmail.com';
    // this.profile.password = '123456';

    this.getLocation();
  }

  ionViewDidEnter() { 
    if(this.navParams.get('backButton')){
      this.view.showBackButton(false);
    }
    if(this.loginService.isLogged()){
      this.popNav();
    }
  }

  getLocation() {

    // Antes de tudo vericar se usuário possui Internet
    if (!this.checkInternet.isInternet()) {
      return false;
    }

    this.loginService.getLocation();

  }

  // login
  login() {

    // FIM data lançamento

    // Antes de tudo vericar se usuário possui Internet
    if (!this.checkInternet.isInternet()) {
      this.withoutInternet();
      return false;
    }

    if (!this.profile.email || !this.profile.password) {
      this.presentAlert("Alerta", "E-mail e senha deve ser preenchidos")
      return;
    }
    
    // TODO add your login code here
    let loader = this.loadingCtrl.create({
      content: 'Carregando...'
    });
    // Show the popup
    loader.present().then(() => {
      this.loginService.login(this.profile).subscribe(result => {
        loader.dismiss();
        if (result.status) {
          //Após realizar o login salvar o usuário no banco de dados local.
          this.saveUserDatabase(this.loginService.getUser());

          // Apresentar a mensagem recebida do servidor
          this.presentToast(result.message);

          this.popNav();

        } else {

          // Apresenta a mensagem de erro vindo do servidor.
          this.presentAlert("Alerta", result.message)

        }
      }, error => {
        loader.dismiss();
        if(error.name === "TimeoutError"){
          this.presentToast('Tente novamente!');
        }
        // remove the popup
        console.log(error);
      });
    });
  }

  /**
   * Login API do FACEBOOK
   * Após buscar os dados (nome, email, ...) é realizado uma busca no nosso servidor.
   */
  loginFacebook() {
    // Antes de tudo vericar se usuário possui Internet
    if (!this.checkInternet.isInternet()) {
      this.withoutInternet()
      return false;
    }

    let env = this;

    let loader = this.loadingCtrl.create({
      content: 'Carregando...'
    });

    let permissions = new Array<string>();
    permissions = ["public_profile", "email"];

    loader.present();
    this.facebook.login(permissions).then((response) => {
      let params = new Array<string>();
      let userId = response.authResponse.userID;
      let accessToken = response.authResponse.accessToken;

      this.facebook.api("/me?fields=name,email", params)
      .then(user => {

          user.picture = `https://graph.facebook.com/${userId}/picture?type=large`;
          let json = {
            img: user.picture,
            first_name: user.name,
            last_name: '',
            email: user.email,
            gender: user.gender,
            authenticate: 'facebook',
            token: accessToken
          }

          // Verificar se facebook retornou o email do usuário
          if (json.email) {

            env.completeLoginExternal(json);

          } else {

            env.presentToast('É preciso ter um e-mail em sua conta do Facebook.');
          }
          loader.dismiss();

      }, (error) => {
        loader.dismiss();
        alert(error);
        console.log('ERRO LOGIN: ',error);
      })
    }, (error) => {
      if(error.name === "TimeoutError"){
        this.presentToast('Tente novamente!');
      }
      console.log('Error logging into Facebook', error);
    });
  }

  /**
   * Após realizar um login externo (Facebook) é realizado a busca do usuário na nossa base
   *
   */
  completeLoginExternal(user) {

    let loader = this.loadingCtrl.create({
      content: 'Carregando...'
    });

    // Show the popup
    loader.present().then(() => {
      //now we have the users info, let's save it in the NativeStorage
      this.loginService.loginExternal(user).subscribe(result => {
        loader.dismiss();
        if (result.status) {

          //Após realizar o login salvar o usuário no banco de dados local.
          this.saveUserDatabase(this.loginService.getUser());

          // Caso tenha alguma informação para mostrar o usuário
          if(result.data.information && result.data.information.status){
            this.presentAlert("", result.data.information.msg);
          }else{
            // Apresentar a mensagem recebida do servidor
            this.presentToast(result.message);
          }

          this.popNav();

        } else {
          this.presentAlert("Alerta", result.message)
        }
      }, error => {
        loader.dismiss();
        // remove the popup
        console.log(error);
      });
    });
  }

  // go to register page
  signUp() {
    this.nav.push(RegisterPage);
  }

  withoutInternet() {
    this.nav.push(WithoutInternetPage);
  }

  // go to recover password page
  recover() {
    this.nav.push(RecoverPage);
  }

  presentAlert(title, subTitle) {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: subTitle,
      buttons: ['Ok']
    });
    alert.present();
  }

  presentToast(message) {
    let toast = this.toastCtrl.create({
      message: message,
      duration: 3000,
      position: 'top'
    });

    toast.present();
  }

  saveUserDatabase(user){
    let model = new User();
    model.id = 1;
    model.json = JSON.stringify(user);

    this.updateUser(model)
    .then(() => {
        console.log("Usuário inserido no banco de dados com sucesso");
      })
      .catch(() => {
        console.log("Erro ao tentar salvar o usuário no banco de dados local");
      });
  }

  // Salva o usuário no banco de dados local
  private updateUser(model) {
    return this.usersProvider.update(model);
  }

  popNav() {
    this.nav.pop();
  }


}



