import {Injectable} from "@angular/core";
import {CONFIG} from "./config";
import { Http, Headers } from '@angular/http';
import 'rxjs/Rx';
import {Observable} from 'rxjs/Observable';

import {LoginService} from "./login-service";

@Injectable()
export class CouponService {
  private coupons:any;

  constructor(public http: Http,
    public loginService:LoginService) {
    this.http = http;
    this.coupons = [];
    this.populate().subscribe(result => {
          this.coupons = result;
    });
  }

  populate() {
    let location = this.loginService.getUser().location;
    if(!location){
      location = {};
      // Lat e Lng Uberlândia Minas Gerais
      location.latitude = -18.914608;
      location.longitude = -48.275380;
    }

    let headers = new Headers();
    headers.append('authorization', this.getToken());
    return this.http.get(CONFIG.url + "/coupon/latitude/"+location.latitude+"/longitude/"+location.longitude, {headers: headers})
      .timeout(10000)
      .map(res => {
        this.coupons = [];
        let coupons = res.json().data;
        for (let i = 0; i < coupons.length; i++) {
          coupons[i].json = JSON.parse(coupons[i].json);
          this.coupons.push(coupons[i]);
        }
        return this.coupons;
      })
      .catch(error => {
        return Observable.throw(error);
      });
  }

  getAll() {
    return this.coupons;
  }
  remove(item) {
    this.coupons.splice(this.coupons.indexOf(item), 1);
  }

  validate(restaurantId, couponName){
    let headers = new Headers();
    headers.append('authorization', this.getToken());
    return this.http.get(CONFIG.url + "/coupon/validate/restaurant/"+restaurantId+"/coupon/"+couponName, {headers: headers})
      .timeout(10000)
      .map(res => {
        return res.json();
      })
      .catch(error => {
        return Observable.throw(error);
      });
  }

  getToken() {
    return this.loginService.getToken();
  }
}



