import {
  Component,
  ViewChild
} from '@angular/core';

import {
  AlertController,
  Content,
  NavController,
  NavParams,
  LoadingController,
  ToastController
} from 'ionic-angular';

import {
  CartService
} from '../../services/cart-service';
import {
  RestaurantService
} from '../../services/restaurant-service';
import {
  LoginService
} from '../../services/login-service';
import {
  UserService
} from '../../services/user-service';
import {
  ItemPage
} from "../item/item";
import {
  CartPage
} from "../cart/cart";

import {
  LoginPage
} from "../login/login";
import {UsersProvider } from '../../providers/users/users';

import { GoogleAnalytics } from '@ionic-native/google-analytics';

import { StatusBar } from '@ionic-native/status-bar';

import { ImageViewerController } from "ionic-img-viewer";
/*
  Generated class for the LoginPage page.

  See http://ionicframework.com/docs/v2/components/#navigation for more info on
  Ionic pages and navigation.
*/
@Component({
  selector: 'page-restaurant',
  templateUrl: 'restaurant.html',
})
export class RestaurantPage {

  @ViewChild("contentRef") contentHandle: Content;
  private tabBarHeight;
  private topOrBottom: string;
  private contentBox;
  detailSegment:string = "cardapio";
  restaurant: any;
  restaurantId: number;

  //Cliente selecionou algum item?
  hasItems: boolean;
  // Realiza entrega para aquela região?
  delivery: boolean;
  hasFavourite: boolean = false;
  header: any;
  ratings:any = [];
  leavePage:boolean = false;
  isLogged:boolean = false;
  promotion:any;

  constructor(private alertCtrl: AlertController,
              public nav: NavController,
              public restaurantService: RestaurantService,
              public cartService: CartService,
              public loginService: LoginService,
              public userService: UserService,
              public navParams: NavParams,
              public ga: GoogleAnalytics,
              private statusBar: StatusBar,
              private imageViewerCtrl: ImageViewerController,
              private loadingCtrl: LoadingController,
              private usersProvider: UsersProvider,
              private toastCtrl: ToastController) {
    // let status bar overlay webview
    this.statusBar.overlaysWebView(false);

    // set status bar to white
    this.statusBar.backgroundColorByHexString('#ffb42d');

    this.ga.trackView('Tela do Restaurante');

    this.ratings = [];
    this.header = {};
    this.hasItems = false;
    this.delivery = false;
    this.restaurantId = navParams.get('id');
    this.isLogged = this.loginService.isLogged();
    
    let promotion = navParams.get('promotion');
    let discount = this.cartService.getDiscount();
    if(promotion && discount && discount.name) {
      if(discount.discount_type == 0 || discount.discount_type == false) {
        this.presentToast("Cupom: R$ "+   discount.value + " foi aplicado.");
      }else{
        this.presentToast("Cupom: " + discount.value + " % de desconto foi aplicado");
      }
    }
    if(!promotion) { 
      let discount  = {
          status: false,
          name: '',
          value: 0.0
        };
      this.cartService.setDiscount(discount);
    }

    this.restaurant = {};
    this.restaurant.categories = [];
    this.findRestaurant();
    this.findRatings();
  }

  findRestaurant() {

    let restaurant = this.restaurantService.getExistById(this.restaurantId);
    //Caso já tenha carregado o restaurante pelo Id não é necessário carregar novamente
    if(restaurant && restaurant.Cuisines){
      this.restaurant = restaurant;
      this.configRestaurant();
    }else{

      let env = this;
      let loader = this.loadingCtrl.create({
        content: 'Carregando...'
      });
      // Show the popup
      loader.present();
      loader.present().then(() => {
        this.restaurantService.getById(this.restaurantId).subscribe(data => {

          setTimeout(() => {
            env.restaurant = this.restaurantService.getAll();
        }, 0);

          this.configRestaurant();

          loader.dismiss();
        }, error => {
          console.log(error);
          loader.dismiss();
          if(error.status === 401){
            this.presentToast("Sua sessão expirou");
            this.goToLogout();
          }
        });
      }, error => {
          loader.dismiss();
          this.presentToast("Desculpe. Tente novamente.");
       });
    }
  }

  findRatings() {
    this.restaurantService.getRatings(this.restaurantId).subscribe(result => {
        this.ratings = result.data;
    }, error => {
      if(error.status === 401){
        this.presentToast("Sua sessão expirou");
        this.goToLogout();
      }
    });
  }

  configRestaurant() {

    let trackName = 'Restaurante: ' + this.restaurant.name;
    this.ga.trackView(trackName);

    //Seta os dados do restaurante no pedido do usuário
    this.cartService.setRestaurant(this.restaurant);

    if(this.isLogged){
      let user = this.loginService.getUser();
      let ids = JSON.stringify(user.favorites_restaurants_ids);

      this.hasFavourite = ids.indexOf(this.restaurantId.toString()) > -1 ? true : false;
    }

    if(this.restaurant && this.restaurant.km_max){
      var kmMax = parseFloat(this.restaurant.km_max);
      if (!isNaN(kmMax) && kmMax > parseFloat(this.restaurant.distance)) {
        this.delivery = true;
      } else {
        this.delivery = false;
      }
    }
  }

  // view item
  viewItem(restaurantId, isOpen, item) {
    this.leavePage = true;
    if (item.disponible) {
      this.nav.push(ItemPage, {
        restaurantId: restaurantId,
        isOpen: isOpen,
        item: JSON.stringify(item)
      });
    } else {
      this.presentToast("Desculpe. Produto indisponível no momento.");
    }
  }

  // go to cart page
  goToCart() {
    this.leavePage = true;
    this.nav.push(CartPage);
  }

  showItems(cat) {
    if (!cat.showItems) {
      cat.showItems = true;
    } else {
      cat.showItems = !cat.showItems;
    }
  }

  findCategoryByid(cat) {
    for (var i = 0; i < this.restaurant.categories.length; i++) {
      if (this.restaurant.categories[i].id === cat.id) {
        return this.restaurant.categories[i];
      }
    }
  }

  ionViewWillEnter() {
    var cart = this.cartService.getCart();
    this.leavePage = false;

    if (cart.orders.restaurant && cart.orders.restaurant.id === this.restaurantId) {
      this.hasItems = cart.orders.items.length ? true : false;
    } else {
      this.hasItems = false;
    }

    if(this.restaurant && this.restaurant.km_max){
      var kmMax = parseFloat(this.restaurant.km_max);
      if (!isNaN(kmMax) && kmMax > parseFloat(this.restaurant.distance)) {
        this.delivery = true;
      } else {
        this.delivery = false;
      }
    }
  }

  ionViewCanLeave() {
    if(!this.leavePage && this.cartService.hasItems()){
      const alert = this.alertCtrl.create({
        title: 'Sair',
        message: 'Deseja realmente sair desta tela? Ao sair seu pedido será excluído do carrinho',
        buttons: [{
            text: 'Não',
            role: 'cancel',
            handler: () => {
            }
          },{
          text: 'Sim',
          handler: data => {

            let navTransition = alert.dismiss();

            // Finalizar alerta
            navTransition.then(() => {
              // Permite sair da tela
              this.leavePage = true;

              // Chama novamente a função ionViewCanLeave
              this.nav.pop();
            });

            // Ainda não sai da tela
            return false;
          }
        }]
      });
      alert.present();
    }else{
      return true;
    }

    // Ao entrar não sai da tela
    return false;
  }

  favorite() {
    var user = this.loginService.getUser();
    var ids = user.favorites_restaurants_ids;

    var msg = '';
    if (!this.hasFavourite) {
      ids.push({
        restaurant: this.restaurantId
      });
      msg = "Adicionando aos favoritos";
    } else {
      msg = "Removendo dos favoritos";
      for (var i = 0; i < ids.length; i++) {
        if (ids[i].restaurant === this.restaurantId) {
          ids.splice(i, 1);
        }
      }
    }

    var favouriteRestaurant = {
      id: user.id,
      favorites_restaurants_ids: JSON.stringify(ids)
    }


    let loader = this.loadingCtrl.create({
      content: msg
    });
    // Show the popup
    loader.present();
    loader.present().then(() => {

      this.userService.favouriteRestaurant(favouriteRestaurant).subscribe(data => {
        loader.dismiss();
        if (data.status) {
          this.loginService.setFavoritesRestaurantsIds(ids);
          if (this.hasFavourite) {
            this.presentToast("Removido da sua lista de favoritos");
          } else {
            this.presentToast("Adicionado na sua lista de favoritos");
          }
          this.hasFavourite = !this.hasFavourite;
        }
      }, error => {
          this.presentToast("Tente novamente");
          console.log(error);
          loader.dismiss();
          if(error.name === "TimeoutError"){
            this.presentToast('Tente novamente!');
          }
          if(error.status === 401){
            this.presentToast("Sua sessão expirou");
            this.goToLogout();
          }
       });
    });
  }

  presentToast(message) {
    let toast = this.toastCtrl.create({
      message: message,
      duration: 3000,
      position: 'top'
    });

    toast.present();
  }

  ionViewDidEnter() {
    this.topOrBottom = this.contentHandle._tabsPlacement;
    this.contentBox = document.querySelector(".scroll-content")['style'];

    if (this.topOrBottom == "top") {
      this.tabBarHeight = this.contentBox.marginTop;
    } else if (this.topOrBottom == "bottom") {
      this.tabBarHeight = this.contentBox.marginBottom;
    }
  }

  getPayment(payment){
    var name = payment.name
    var card = payment.card ? payment.card : '';
    var online =  card.length && payment.is_online === 0 ? '(máquina)' : '';
    return name + ' ' + card + ' ' + online;
  }

  getMedia(rate) {

    if(rate.rate_delivery){
      let media = (rate.rate + rate.rate_food + rate.rate_delivery)  / 3;
      return media.toFixed(2);
    }else{
      let media = (rate.rate + rate.rate_food)  / 2;
      return media.toFixed(2);
    }
  }

  getFrete(restaurant) {
    if(typeof restaurant.frete === 'string'){
      return true;
    }
    return false;
  }

  getCircle(index, size){
    return index+1 < size ? '• ' : '';
  }

  // go to login page
  goToLogout() {
    this.leavePage = true;
    this.updateUser();
    this.nav.push(LoginPage);
  }
  // Remove o usuário do banco de dados local
  private updateUser() {
      return this.usersProvider.update({id: 1, json: '{}'});
  }

  isPromition(category) {
    if(category.name && category.name.toUpperCase() === 'PROMOÇÕES'){
      this.promotion = category;
      return true;
    }
    return false;
  }

  canShowItem(item) {

    if(!item.valid_days){
      return true;
    }

    return item.valid_days.indexOf(this.restaurant.week) > -1 ? true : false;

  }

  isShowCuisines(category) {
    if(category.activated === false){
      return false;
    }
    return true;
  }

  zoomClickImage(event, img) {
    const viewer = this.imageViewerCtrl.create(img)
    viewer.present();
    event.stopPropagation();
  }

}



