import { Component } from '@angular/core';
import { NavController, ToastController } from 'ionic-angular';

import {
	UserService
} from '../../services/user-service';

import {UserPage} from "../user/user";

import {
	UsersProvider
} from '../../providers/users/users';

import {
  HistoryDetailsPage
} from '../order/details/history-detail';

import {
  HistoryOrderService
} from '../../services/history-order-service';

import { StatusBar } from '@ionic-native/status-bar';
/**
 * Generated class for the DigitalWalletPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@Component({
  selector: 'page-digital-wallet',
  templateUrl: 'digital-wallet.html',
})
export class DigitalWalletPage {

	wallet:any;
	showSpinner:boolean = false;
	constructor(public nav: NavController,
		private toastCtrl: ToastController,
		private statusBar: StatusBar,
		private usersProvider: UsersProvider,
		public historyOrderService: HistoryOrderService,
		private userService: UserService) {

		// let status bar overlay webview
	    this.statusBar.overlaysWebView(false);

	    // set status bar to white
	    this.statusBar.backgroundColorByHexString('#ffb42d');

		this.wallet = {};
	}

	ionViewDidLoad() {
		console.log('ionViewDidLoad DigitalWalletPage');
	}

	ionViewDidEnter() {
		this.showSpinner = true;
		this.userService.getBalance().subscribe(result => {
	        this.wallet = result;
	        this.showSpinner = false;
	      }, error => {
	        console.log(error);
	        this.showSpinner = false;
	        if(error.status === 401){
	          this.presentToast("Sua sessão expirou");
	          this.goToLogout();
	        }
	      });

		this.historyOrderService.getByUser().subscribe(data => {
	      
	    }, error => {
	      console.log(error);
	      if(error.status === 401){
	        this.presentToast("Sua sessão expirou");
	        this.goToLogout();
	      }
	    });
	}

	presentToast(message) {
		let toast = this.toastCtrl.create({
			message: message,
			duration: 5000,
			position: 'top'
		});

		toast.present();
	}

	// go to login page
	goToLogout() {
		this.updateUser();
		this.nav.push(UserPage);
	}

	// Remove o usuário do banco de dados local
	private updateUser() {
		return this.usersProvider.update({
			id: 1,
			json: '{}'
		});
	}

	getMoney(item) {
		if(item.is_valid)
			return "R$ " + item.credit.toFixed(2);
		return "R$ " + (item.total * (item.percentage/ 100)).toFixed(2)
	}

	getDetails(item) {
	    this.nav.push(HistoryDetailsPage, {
	      orderId: item.orders_id
	    });
	}

}



