import {Component} from '@angular/core';
import {LoadingController, NavController} from 'ionic-angular';

import {NewAddressPage} from './new-address/new-address';
import {LoginService} from '../../services/login-service';
import {AddressService} from '../../services/address-service';

import { StatusBar } from '@ionic-native/status-bar';
/*
  Generated class for the LoginPage page.

  See http://ionicframework.com/docs/v2/components/#navigation for more info on
  Ionic pages and navigation.
*/
@Component({
  selector: 'page-address',
  templateUrl: 'address.html',
})
export class AddressPage {

 	address:any;
  	constructor(public nav: NavController,
                private statusBar: StatusBar,
        				public addressService: AddressService,
        				public loginService: LoginService,
                private loadingCtrl: LoadingController) {

      this.address = this.addressService.getAll();
      if(!this.address.length){
        this.findAddress();  
      }

      // let status bar overlay webview
      this.statusBar.overlaysWebView(false);

      // set status bar to white
      this.statusBar.backgroundColorByHexString('#ffb42d');
  	}

    findAddress(){
      // Create the popup
      let loader = this.loadingCtrl.create({
        content: 'Buscando endereços...'
      });
      // Show the popup
      loader.present();
      loader.present().then(() => {
        this.addressService.findById(this.loginService.getUser().id).subscribe(data => {
              // remove the popup
              loader.dismiss();
              this.address = data.data;
           }, error => {
               console.log(error);
               // remove the popup
              loader.dismiss();
           });
      });
    }

  	newAddress(){
  		this.nav.push(NewAddressPage, {userId: this.loginService.getUser().id});
  	}

  	editAddress(address) {
  		this.nav.push(NewAddressPage, {address: JSON.stringify(address)});
  	}

    ionViewWillEnter() {
      this.address = this.addressService.getAll();
    }
}


