import {
  Component
} from '@angular/core';
import {
  App,
  AlertController,
  LoadingController,
  NavController,
  NavParams,
  ToastController
} from 'ionic-angular';

import { GoogleAnalytics } from '@ionic-native/google-analytics';

import {
  Http
} from '@angular/http';

/* Services */
import {
  RestaurantService
} from '../../../services/restaurant-service';

import {
  LoginService
} from "../../../services/login-service";

import {
  CartService
} from '../../../services/cart-service';

import {
  PromotionService
} from '../../../services/promotion-service';

import {
  UsersProvider 
} from '../../../providers/users/users';


/* Pages */

import {
  RestaurantPage
} from "../../restaurant/restaurant";

import {
  WithoutInternetPage
} from "../../without-internet/without-internet";

import {
  CheckInternet
} from '../../../services/check-internet';

import {
  LoginPage
} from "../../login/login";

import { StatusBar } from '@ionic-native/status-bar';
/*
  Generated class for the LoginPage page.

  See http://ionicframework.com/docs/v2/components/#navigation for more info on
  Ionic pages and navigation.
*/
@Component({
  selector: 'page-list-restaurants',
  templateUrl: 'list-restaurants.html',
})
export class ListRestaurantsPage {
  banner = 'assets/img/banner.png';
  restaurants: any;
  message: string;
  location: any;
  showSpinner: boolean;
  offset: number;
  promotionId:any;
  promotion:any;

  constructor(public app: App,
    public nav: NavController,
    public navParams: NavParams,
    private loadingCtrl: LoadingController,
    private alertCtrl: AlertController,
    private toastCtrl: ToastController,
    private checkInternet: CheckInternet,
    public ga: GoogleAnalytics,
    public http: Http,
    private statusBar: StatusBar,
    private restaurantService: RestaurantService,
    private cartService: CartService,
    private loginService: LoginService,
    private usersProvider: UsersProvider,
    private promotionService: PromotionService) {

    // let status bar overlay webview
    this.statusBar.overlaysWebView(false);

    // set status bar to white
    this.statusBar.backgroundColorByHexString('#ffb42d');

    this.ga.trackView('Tela Promoções Restaurantes');

    this.location = {};
    this.showSpinner = false;

    this.promotionId = navParams.get('promotionId');
    this.promotion = this.promotionService.getById(this.promotionId);
    this.restaurants = this.promotionService.getRestaurants(this.promotionId);
  }

  // view restaurant detail
  viewRestaurant(restaurant) {
    let id = restaurant.id;

    restaurant = this.restaurantService.getExistById(id);
    //Caso já tenha carregado o restaurante pelo Id não é necessário carregar novamente
    if (restaurant && restaurant.Cuisines) {
      this.app.getRootNav().push(RestaurantPage, {
        id: id,
        promotion: true
      })
    } else {
      let loader = this.loadingCtrl.create({
        content: 'Carregando...'
      });
      // Show the popup
      loader.present();
      loader.dismiss().then(() => {
        this.restaurantService.getById(id).subscribe(data => {
          this.app.getRootNav().push(RestaurantPage, {
            id: id,
            promotion: true
          })
        }, error => {
          if(error.status === 401){
            this.presentToast("Sua sessão expirou");
            this.goToLogout();
          }
          console.log(error);
        });
      }, error => {
        loader.dismiss();
        this.presentToast("Desculpe. Tente novamente.");
      });
    }
  }

  // Busca os restaurentes na base de dados
  findRestaurant() {
    // Antes de tudo vericar se usuário possui Internet
    if (!this.checkInternet.isInternet()) {
      return false;
    }

    this.showSpinner = true;
    let location = this.restaurantService.getUserLocation();
    if (location && location.latitude && location.longitude) {
      this.restaurantService.getInitialHome().subscribe(result => {
        this.restaurants = this.restaurantService.getAll();
        this.message = result.message;
        this.showSpinner = false;
      }, error => {
        console.log(error);
        this.showSpinner = false;
        if(error.status === 401){
          this.presentToast("Sua sessão expirou");
          this.goToLogout();
        }
      });
    }
  }

  // Tela de sem acesso a internet
  withoutInternet() {
    this.app.getRootNav().push(WithoutInternetPage);
  }


  ionViewDidEnter() {
    
    // Antes de tudo vericar se usuário possui Internet
    if (!this.checkInternet.isInternet()) {
      this.withoutInternet();
      return false;
    }

    this.location = this.loginService.getUserLocation();

  }


  presentToast(message) {
    let toast = this.toastCtrl.create({
      message: message,
      duration: 3000,
      position: 'top'
    });

    toast.present();
  }

  getCircle(index, size) {
    return index + 1 < size ? '• ' : '';
  }

  presentAlert(title,subTitle) {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: subTitle,
      buttons: ['Ok']
    });
    alert.present();
  }

  getShowSpinner() {
    if(!this.location || !this.location.street ) {
      return false;
    }
    return this.showSpinner;
  }

  /* go to login page */
  goToLogout() {
    this.updateUser();
    this.app.getRootNav().setRoot(LoginPage);
  }

  getFrete(restaurant) {

    let frete = this.cartService.calculateFrete(restaurant.frete,
      restaurant.frete_km, restaurant.km_max,
      restaurant.km_normal, restaurant.distance);

    if(typeof frete === 'string'){
      return true;
    }
    return false;
  }

  /* Remove o usuário do banco de dados local */
  private updateUser() {
      return this.usersProvider.update({id: 1, json: '{}'});
  }

}



