import {
  Component
} from '@angular/core';
import {
  AlertController,
  LoadingController,
  NavController,
  ToastController
} from 'ionic-angular';

import {
  LoginService
} from '../../services/login-service';

import {
  ChangePasswordPage
} from "../change-password/change-password";

import {
  UsersProvider,
  User
} from '../../providers/users/users';

import {
  CheckInternet
} from '../../services/check-internet';

import {
  LoginPage
} from "../login/login";

import { StatusBar } from '@ionic-native/status-bar';
@Component({
  selector: 'page-settings',
  templateUrl: 'settings.html',
})
export class SettingsPage {
  // dummy data for users
  user = {};

  constructor(public nav: NavController,
    private toastCtrl: ToastController,
    private alertCtrl: AlertController,
    private loadingCtrl: LoadingController,
    private checkInternet: CheckInternet,
    private usersProvider: UsersProvider,
    private statusBar: StatusBar,
    public loginService: LoginService) {

    // let status bar overlay webview
    this.statusBar.overlaysWebView(false);

    // set status bar to white
    this.statusBar.backgroundColorByHexString('#ffb42d');

    this.user = loginService.getUser();
  }

  goToChangePassword() {
    this.nav.push(ChangePasswordPage);
  }

  updateUser() {
    // Antes de tudo vericar se usuário possui Internet
    if (!this.checkInternet.isInternet()) {
      this.presentToast("Sem acesso a internet");
      return false;
    }

    // Create the popup
    let loader = this.loadingCtrl.create({
      content: 'Atualizando...'
    });
    // Show the popup
    loader.present();
    loader.present().then(() => {
      this.loginService.updateUser(this.user).subscribe(data => {
        // remove the popup
        loader.dismiss();
        if (data.status) {
          this.presentToast(data.message);

          //Após editar o usuário atualiza também o banco de dados local.
          this.saveUserDatabase(this.loginService.getUser());

        } else {
          this.presentAlert(data.message)
        }
      }, error => {
        if (error.status === 401) {
          this.presentToast("Sua sessão expirou");
          this.goToLogout();
        } else {
          this.presentAlert('Tente novamente!')
        }
        console.log(error);
        // remove the popup
        loader.dismiss();
      });
    });

  }

  presentAlert(subTitle) {
    let alert = this.alertCtrl.create({
      title: 'Alerta',
      subTitle: subTitle,
      buttons: ['Ok']
    });
    alert.present();
  }

  presentToast(message) {
    let toast = this.toastCtrl.create({
      message: message,
      duration: 3000,
      position: 'top'
    });

    toast.present();
  }

  saveUserDatabase(user) {
    let model = new User();
    model.id = 1;
    model.json = JSON.stringify(user);

    this.updateUserLocal(model)
      .then(() => {
        console.log("Usuário inserido no banco de dados com sucesso");
      })
      .catch(() => {
        console.log("Erro ao tentar salvar o usuário no banco de dados local");
      });
  }

  // Salva o usuário no banco de dados local
  private updateUserLocal(model) {
    return this.usersProvider.update(model);
  }

  // go to login page
  goToLogout() {
    this.updateUserLocal({
      id: 1,
      json: '{}'
    })
    this.nav.push(LoginPage);
  }

}


