import {
  Component
} from '@angular/core';
import {
  AlertController,
  NavController,
  LoadingController,
  ToastController
} from 'ionic-angular';
import {
  FormBuilder,
  Validators
} from '@angular/forms';

import {
  LoginService
} from '../../services/login-service';
import {
  UserService
} from '../../services/user-service';

import {
  CheckInternet
} from '../../services/check-internet';

import {
  LoginPage
} from "../login/login";

import {
  UsersProvider
} from '../../providers/users/users';

import { StatusBar } from '@ionic-native/status-bar';

@Component({
  selector: 'page-change-password',
  templateUrl: 'change-password.html',
})
export class ChangePasswordPage {

  register: any;
  profile: any;

  constructor(public nav: NavController,
    public userService: UserService,
    public loginService: LoginService,
    private toastCtrl: ToastController,
    private alertCtrl: AlertController,
    public formBuilder: FormBuilder,
    private statusBar: StatusBar,
    private checkInternet: CheckInternet,
    private usersProvider: UsersProvider,
    private loadingCtrl: LoadingController) {

    // let status bar overlay webview
    this.statusBar.overlaysWebView(false);

    // set status bar to white
    this.statusBar.backgroundColorByHexString('#ffb42d');
    
    this.profile = {};

    this.register = this.formBuilder.group({
      oldPassword: ['', Validators.compose([Validators.minLength(6),Validators.required])],
      newPassword: ['', Validators.compose([Validators.minLength(6),Validators.required])],
      repeatPassword: ['', Validators.compose([Validators.minLength(6),Validators.required])]
    });
  }

  // register
  signUp() {
    // Antes de tudo vericar se usuário possui Internet
    if (!this.checkInternet.isInternet()) {
      this.presentToast("Sem acesso a internet");
      return false;
    }

    if (this.profile.newPassword !== this.profile.repeatPassword) {
      this.presentAlert("Senhas não são iguais.");
      return;
    }

    if (this.profile.newPassword.length < 6) {
      this.presentAlert("A senha deve ter pelo menos 6 caracteres.");
      return;
    }

    this.profile.id = this.loginService.getUser().id;
    // Create the popup
    let loader = this.loadingCtrl.create({
      content: 'Modificando...'
    });
    // Show the popup
    loader.present();
    loader.present().then(() => {

      this.userService.changePassword(this.profile).subscribe(data => {
        // remove the popup
        loader.dismiss();
        if (data.status) {
          this.presentToast(data.message);
          this.register.reset()
        } else {
          this.presentAlert(data.message)
        }
      }, error => {
        console.log(error);
        // remove the popup
        loader.dismiss();
        if(error.name === "TimeoutError"){
          this.presentToast('Tente novamente!');
        }
        if (error.status === 401) {
          this.presentToast("Sua sessão expirou");
          this.goToLogout();
        }
      });
    });
  }

  presentAlert(subTitle) {
    let alert = this.alertCtrl.create({
      title: 'Alerta',
      subTitle: subTitle,
      buttons: ['Ok']
    });
    alert.present();
  }

  presentToast(message) {
    let toast = this.toastCtrl.create({
      message: message,
      duration: 3000,
      position: 'top'
    });

    toast.present();
  }

  // go to login page
  goToLogout() {
    this.updateUser();
    this.nav.push(LoginPage);
  }
  // Remove o usuário do banco de dados local
  private updateUser() {
      return this.usersProvider.update({id: 1, json: '{}'});
  }
}


