import {Component} from '@angular/core';
import {AlertController, NavController, ToastController} from 'ionic-angular';

import {DeliveryPage} from '../delivery/delivery';
import {CartService} from '../../services/cart-service';

import { GoogleAnalytics } from '@ionic-native/google-analytics';

import {
  LoginService
} from "../../services/login-service";

import {
  LoginPage
} from "../login/login";

import { StatusBar } from '@ionic-native/status-bar';
/*
  Generated class for the LoginPage page.

  See http://ionicframework.com/docs/v2/components/#navigation for more info on
  Ionic pages and navigation.
*/
@Component({
  selector: 'page-cart',
  templateUrl: 'cart.html',
})
export class CartPage {
  cart: any;
  isLogged:boolean = false;
  isCashBack:boolean = false;
  constructor(private nav: NavController,
              private cartService: CartService,
              private toastCtrl: ToastController,
              public ga: GoogleAnalytics,
              private statusBar: StatusBar,
              private loginService: LoginService,
              private alertCtrl: AlertController) {

    // let status bar overlay webview
    this.statusBar.overlaysWebView(false);

    // set status bar to white
    this.statusBar.backgroundColorByHexString('#ffb42d');

    this.ga.trackView('Tela Carrinho');
    // set sample data
    this.cart = cartService.getCart();
  }


  nameItemRequired(item){
    for (let i = 0; i < item.options.required.length; i++) {
      if(item.options.required[i].id === parseInt(item.itemRequired)){
        return item.options.required[i];
      }
    }
  }

  // plus quantity
  plusQty(position, item) {
    item.quantity++;
    this.cartService.updateItem(position, item);
  }

  // minus quantity
  minusQty(position, item) {
    if (item.quantity > 1){
      item.quantity--;
      this.cartService.updateItem(position, item);
    }else{
      this.removeItem(item);
    }
  }

  optional(item){
    let array = [];
    for (let i = 0; i < item.options.optional.length; i++) {
      if(item.options.optional[i].selected){
        array.push(item.options.optional[i]);
      }
    }
    return array;
  }

  removeItem(item){
    this.presentConfirm(item);
  }

  presentConfirm(item) {
    let alert = this.alertCtrl.create({
      title: 'Remover produto',
      message: 'Deseja remover da sua lista?',
      buttons: [
        {
          text: 'Não',
          role: 'cancel',
          handler: () => {
            //console.log('Cancel clicked');
          }
        },
        {
          text: 'Sim',
          handler: () => {
            this.cartService.remove(item);
          }
        }
      ]
    });
    alert.present();
  }

  // go to the devivery page
  formPayment(){
    if(this.isLogged){
      this.nav.push(DeliveryPage);
    }else{
      this.presentConfirmLogin();
    }
  }

  presentToast(message) {
      let toast = this.toastCtrl.create({
        message: message,
        duration: 3000,
        position: 'top'
      });

      toast.present();
  }

  presentConfirmLogin() {
    let env = this;
    let alert = this.alertCtrl.create({
      title: 'Deseja fazer login?',
      message: 'Para continuar é necessário realizar o login.',
      buttons: [{
        text: 'Não',
        role: 'cancel',
        handler: () => {
          console.log('Cancel clicked');
        }
      }, {
        text: 'Sim',
        handler: () => {
          env.nav.push(LoginPage);
        }
      }]
    });
    alert.present();
  }

  getTotal(cart) {
    return this.cartService.getTotal().toFixed(2);
  }

  getFrete( frete ) {
      if(isNaN(frete)){
        return frete;
      }else{
        return "R$ " + frete.toFixed(2);
      }
  }

  getDiscount(discount) {

    if (discount.discount_type === "1" || discount.discount_type === true) {
      return discount.value + "%";
    } else {
      return "R$ " + discount.value;
    }
  }

  ionViewDidEnter() {
     this.isCashBack = this.cart.cashback && this.cart.cashback > 0 ? true : false; 
    this.isLogged = this.loginService.isLogged();
  }

}



