import {
	Component
} from '@angular/core';
import {
	NavController,
	NavParams,	
	ToastController
} from 'ionic-angular';

import {
  CheckInternet
} from '../../services/check-internet';

import {
  MainTabsPage
} from "../main-tabs/main-tabs";

import { StatusBar } from '@ionic-native/status-bar';

@Component({
	selector: 'page-without-internet',
	templateUrl: 'without-internet.html',
})
export class WithoutInternetPage {

	constructor(public nav: NavController,
		public navParams: NavParams,
		private toastCtrl: ToastController,
		private statusBar: StatusBar,
		private checkInternet: CheckInternet) {
		// let status bar overlay webview
		this.statusBar.overlaysWebView(false);

		// set status bar to white
		this.statusBar.backgroundColorByHexString('#ffb42d');
	}

	tryConnection() {
		// Antes de tudo vericar se usuário possui Internet
		let connection = this.checkInternet.isInternet();
		if (!connection) {
			this.presentToast("Sem conexão!");
			return connection;
		}
		this.nav.push(MainTabsPage);
	}

	ionViewDidLoad() {
		console.log('ionViewDidLoad WithoutInternetPage');
	}


	presentToast(message) {
    let toast = this.toastCtrl.create({
      message: message,
      duration: 3000,
      position: 'top'
    });

    toast.present();
  }

}


