import {Component} from '@angular/core';
import {App, Platform} from 'ionic-angular';
import {ViewChild} from '@angular/core';
import {StatusBar} from '@ionic-native/status-bar';
import {SplashScreen} from '@ionic-native/splash-screen';

import { Events } from 'ionic-angular'

import {
  OneSignal
} from '@ionic-native/onesignal';

import {
  CartService
} from '../services/cart-service';

import {
  LoginService
} from '../services/login-service';

import {
  HistoryOrderService
} from '../services/history-order-service';

import {
  CheckInternet
} from '../services/check-internet';

import {
  MainTabsPage
} from "../pages/main-tabs/main-tabs";

import {
  AppVersionService
} from '../services/app-version-service';


import {
  AppVersionPage
} from "../pages/app-version/app-version";

import {
  ConstructionPage
} from "../pages/construction/construction";

import {
  WithoutInternetPage
} from "../pages/without-internet/without-internet";

import {
  DatabaseProvider
} from "../providers/database/database";


import { GoogleAnalytics } from '@ionic-native/google-analytics';


@Component({
  templateUrl: 'app.html',
  queries: {
    nav: new ViewChild('content')
  }
})
export class MyApp {

  public rootPage: any;

  public nav: any;

  private myConnection: any;

  constructor(public app: App,
    platform: Platform, 
    statusBar: StatusBar, 
    private splashScreen: SplashScreen,
    private oneSignal: OneSignal,
    private cartService: CartService,
    private loginService: LoginService, 
    private historyOrderService: HistoryOrderService,
    private appVersionService: AppVersionService,
    private checkInternet: CheckInternet,
    private ga: GoogleAnalytics,
    public events: Events,
    private dbProvider: DatabaseProvider) {
    this.myConnection = {};

    var env = this;
    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault();

      env.getOneSignal();

      this.appVersion();
    });
  }

  private openHomePage() {
    this.splashScreen.hide();
    this.rootPage = MainTabsPage;
  }

  private withoutInternet() {
    this.splashScreen.hide();
    this.rootPage = WithoutInternetPage;
  }

  private serverNotWorking() {
    this.splashScreen.hide();
    this.app.getRootNav().push(ConstructionPage);    
  }

  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot(page.component);
  }

  appVersion(){
    // Antes de tudo vericar se usuário possui Internet
    if (!this.checkInternet.isInternet()) {
      this.withoutInternet();
      return false;
    }
    
    this.appVersionService.populate().subscribe(result => {
      let currentVersion = this.appVersionService.getNewVersion();
      let appVersion = this.appVersionService.getAppVersion();
      
      //Inicializa o Analytics depois que carregar a versão do aplicativo
      this.initGoogleAnalytics();

      if(currentVersion && appVersion && currentVersion !== appVersion){
        this.redirectAppVersionPage();
      }else{
        //Criando o banco de dados
        this.dbProvider.createDatabase()
          .then(() => {
            // fechando a SplashScreen somente quando o banco for criado
            this.openHomePage();
          })
          .catch(() => {
            // ou se houver erro na criação do banco
            this.openHomePage();
          });
      }
    }, error => {
      this.serverNotWorking();
      console.log(error);
    });
  }

  redirectAppVersionPage() {
    this.splashScreen.hide();
    this.app.getRootNav().push(AppVersionPage);
  }

  getOneSignal() {
    var env = this;
    env.oneSignal.startInit('5b071cbb-f263-4342-bf22-a9f5dd9f8d4a', '1030970276640');
    env.oneSignal.inFocusDisplaying(env.oneSignal.OSInFocusDisplayOption.InAppAlert);
    
    env.oneSignal.getIds().then((ids) => {
      //alert(ids.userId);
      console.log(ids.userId);
      env.cartService.setPushUserId(ids.userId);
      //ids.pushToken
    });


    env.oneSignal.handleNotificationReceived().subscribe(result => {
      // do something when notification is received
      if(result && result.payload && result.payload.additionalData) {
        this.events.publish('order:update', result.payload.additionalData, Date.now());
      }
    });

    env.oneSignal.handleNotificationOpened().subscribe(result => {
      // do something when a notification is opened
      if(env.loginService.getUser() && env.loginService.getUser().id){
        env.historyOrderService.getByUser().subscribe(result => {});
      }
    });

    env.oneSignal.endInit();
  }

  initGoogleAnalytics() {
    var trackingId = 'UA-112991576-1';
    if (/(android)/i.test(navigator.userAgent)) { // for android 
      trackingId = 'UA-112991576-1';
    } else if (/(ipod|iphone|ipad)/i.test(navigator.userAgent)) { // for ios
      trackingId = 'UA-112991576-1';
    }
 
    this.ga.debugMode();

    this.ga.setAppVersion(this.appVersionService.getNewVersion());
    this.ga.setAllowIDFACollection(true);
    
    this.ga.startTrackerWithId(trackingId).then(()=> {
    console.log("GoogleAnalytics Initialized with ****** : " + trackingId);

    this.ga.enableUncaughtExceptionReporting(true)
        .then((_success) => {
          console.log("GoogleAnalytics enableUncaughtExceptionReporting Enabled.");
        }).catch((_error) => {
        console.log("GoogleAnalytics Error enableUncaughtExceptionReporting : " + _error)
      });
    });
  }

} 



