import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';

import {
	LoginService
} from "../../services/login-service";

import { StatusBar } from '@ionic-native/status-bar';
/**
 * Generated class for the CitiesPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@Component({
  selector: 'page-cities',
  templateUrl: 'cities.html',
})
export class CitiesPage {

	cities:any;
	bkpCities:any;
	constructor(public nav: NavController, 
		public navParams: NavParams,
		private statusBar: StatusBar,
		private loginService: LoginService) {

		// let status bar overlay webview
	    this.statusBar.overlaysWebView(false);

	    // set status bar to white
	    this.statusBar.backgroundColorByHexString('#ffb42d');
	    
		this.cities = [{
			street: 'Uberlândia  - MG',
			latitude: -18.8960045,
			longitude: -48.2678202
		}, {
			street: 'Araguari - MG',
			latitude: -18.543019,
			longitude: -48.159795
		}];
		this.bkpCities = this.cities;
	}

	ionViewDidLoad() {
	
	}

	getCity(city) {
		this.loginService.setLocation(city);
		this.nav.pop();
	}

	// filter list cities
	filterCity(ev: any) {

		// set val to the value of the searchbar
		let val = ev.target.value;

		// if the value is an empty string don't filter the items
		if (val && val.trim() != '') {
		  this.cities = this.bkpCities.filter((item) => {
		    return (item.street.toLowerCase().indexOf(val.toLowerCase()) > -1);
		  })
		}
	}

}


