import {Injectable} from "@angular/core";
import {CONFIG} from "./config";
import { Http, Headers } from '@angular/http';
import 'rxjs/Rx';
import {Observable} from 'rxjs/Observable';

import {LoginService} from "./login-service";

@Injectable()
export class PromotionService {
  private promotions:any;

  constructor(public http: Http,
    public loginService:LoginService) {    
    this.http = http;
    this.promotions = [];
    this.populate().subscribe(result => {
          this.promotions = result;
    });
  }

  populate() { 
    let location = this.loginService.getUser().location;
    if(!location){
      location = {}; 
      // Lat e Lng Uberlândia Minas Gerais
      location.latitude = -18.914608
      location.longitude = -48.275380
    }

    let headers = new Headers();
    headers.append('authorization', this.getToken());
    return this.http.get(CONFIG.url + "/promotion/restaurant/latitude/"+location.latitude+"/longitude/"+location.longitude, {headers: headers})
      .map(res => {
        return res.json().data;
      })
      .catch(error => {
        return Observable.throw(error);
      }); 
  }

  getAll() {
    return this.promotions;  
  }

  getRestaurants(promotionId) {
    for (var i = 0; i < this.promotions.length; i++) {
      if(this.promotions[i].id === promotionId){
        return this.promotions[i].Restaurants   
      }
    }
    return [];
  }

  getById(id) {
    for (var i = 0; i < this.promotions.length; i++) {
      if(this.promotions[i].id === id){
        return this.promotions[i];
      }
    }
    return {};
  }


  getToken() {
    return this.loginService.getToken();
  }
}


