import {Injectable} from "@angular/core";
import {CONFIG} from "./config";
import { Http } from '@angular/http';
import 'rxjs/Rx';

import { AppVersion } from '@ionic-native/app-version';

import { Platform } from 'ionic-angular';

import {
  Observable
} from 'rxjs/Observable';

@Injectable()
export class AppVersionService {

  private newVersion:any;
  private appVersion:any;

  constructor(public http: Http,
    public plt: Platform,
    private version: AppVersion) {
    this.http = http;
    this.getCurrentAppVersion();

  }

  // Busca nas configurações em qual versão se encontra o aplicativo.
  getCurrentAppVersion() {
    this.version.getVersionCode().then(result => {
      this.appVersion = result.toString();
      console.log(this.appVersion);
    })
    .catch(error => {
      this.appVersion = null;
      console.log(error);
      return Observable.throw(error);
    });
  }

  // Busca no servidor em qual versão deve ser executado o aplicativo
  populate() {
    return this.http.get(`${CONFIG.url}/app-version/last-version/origin/${this.plt._platforms.toString()}`)
      .map(res => {
        this.newVersion = res.json().data.version;
        return res.json();
      })
      .catch(error => {
        console.log("Passei aqui truta");
        return Observable.throw(error);
      });
  }

  getAppVersion() {
    return this.appVersion;
  }

  getNewVersion() {
    return this.newVersion;
  }

}



