import {
	Component
} from '@angular/core';

import {
	AlertController,
	NavController,
	LoadingController,
	ToastController
} from 'ionic-angular';

import {
	FormBuilder,
	Validators
} from '@angular/forms';

import {
  LoginPage
} from "../login/login";

import {
	LoginService
} from '../../services/login-service';
import {
	UserService
} from '../../services/user-service';
import {
	UsersProvider
} from '../../providers/users/users';

import { StatusBar } from '@ionic-native/status-bar';
/*
  Generated class for the LoginPage page.

  See http://ionicframework.com/docs/v2/components/#navigation for more info on
  Ionic pages and navigation.
*/
@Component({
	selector: 'page-help',
	templateUrl: 'help.html',
})
export class HelpPage {

	form: any;
	constructor(public nav: NavController,
		private toastCtrl: ToastController,
		private alertCtrl: AlertController,
		private loadingCtrl: LoadingController,
		private statusBar: StatusBar,
		private userService: UserService,
		public loginService: LoginService,
		private usersProvider: UsersProvider,
		public formBuilder: FormBuilder) {

		// let status bar overlay webview
	    this.statusBar.overlaysWebView(false);

	    // set status bar to white
	    this.statusBar.backgroundColorByHexString('#ffb42d');

		this.form = this.formBuilder.group({
			message: ['', Validators.required]
		});
	}

	sendEmail() {
		let profile = this.loginService.getUser();

		// Prepara os inputs para enviar ao servidor (nome, email e mensagem)
		let json = {
			name: profile.first_name + " " + profile.last_name,
			email: profile.email,
			phone: "("+profile.country_code+ ") " + profile.phone,
			message: this.form.value.message
		}
		// Create the popup
		let loader = this.loadingCtrl.create({
			content: 'Aguarde estamos a enviando sua mensagem...'
		});
		// Show the popup
		loader.present();
		loader.present().then(() => {
			this.userService.sendEmailToSaciaFome(json).subscribe(data => {
				// remove the popup
				loader.dismiss();
				if (data.status) {
					this.presentToast(data.message);
					this.form.reset()
				} else {
					this.presentAlert(data.message)
				}
			}, error => {
				console.log(error);
				if(error.name === "TimeoutError"){
		            this.presentToast('Tente novamente!');
		        }
				// remove the popup
				loader.dismiss();
				if (error.status === 401) {
					this.presentToast("Sua sessão expirou");
					this.goToLogout();
				}
			});
		});
	}

	presentAlert(subTitle) {
		let alert = this.alertCtrl.create({
			title: 'Alerta',
			subTitle: subTitle,
			buttons: ['Ok']
		});
		alert.present();
	}

	presentToast(message) {
		let toast = this.toastCtrl.create({
			message: message,
			duration: 5000,
			position: 'top'
		});

		toast.present();
	}

	// go to login page
	goToLogout() {
		this.updateUser();
		this.nav.push(LoginPage);
	}
	// Remove o usuário do banco de dados local
	private updateUser() {
		return this.usersProvider.update({
			id: 1,
			json: '{}'
		});
	}
}


