import {Component} from '@angular/core';
import {App, LoadingController, NavController, ToastController} from 'ionic-angular';

import {RestaurantService} from '../../services/restaurant-service';
import {RestaurantPage} from "../restaurant/restaurant";
import {CategoryService} from '../../services/category-service';
import {LoginService} from '../../services/login-service';

import {
  LocationPage
} from '../location/location';

import {
  LoginPage
} from "../login/login";

import {
  CartService
} from '../../services/cart-service';

import {UsersProvider } from '../../providers/users/users';

import { StatusBar } from '@ionic-native/status-bar';

/*
  Generated class for the LoginPage page.

  See http://ionicframework.com/docs/v2/components/#navigation for more info on
  Ionic pages and navigation.
*/
@Component({
  selector: 'page-favorite',
  templateUrl: 'favorite.html',
})
export class FavoritePage {
  banner = 'assets/img/banner.png'; 
  restaurants: any;
  location: any;

  constructor(public app: App,
              public nav: NavController,
              public categoryService: CategoryService,
              public restaurantService: RestaurantService,
              private toastCtrl: ToastController,
              private statusBar: StatusBar,
              public loginService: LoginService,
              private usersProvider: UsersProvider,
              public cartService: CartService,
              private loadingCtrl: LoadingController) {

    // let status bar overlay webview
    this.statusBar.overlaysWebView(false);

    // set status bar to white
    this.statusBar.backgroundColorByHexString('#ffb42d');

    this.location = {};
    this.findRestaurant();
  }

  // view restaurant detail
  viewRestaurant(restaurant) {
    let id = restaurant.id;
    restaurant = this.restaurantService.getExistById(id);
    //Caso já tenha carregado o restaurante pelo Id não é necessário carregar novamente
    if(restaurant && restaurant.Cuisines){
      this.app.getRootNav().push(RestaurantPage, {
            id: id
          })
    }else{
      let loader = this.loadingCtrl.create({
          content: 'Carregando...'
        });
      // Show the popup
      loader.present();
      loader.present().then(() => {
        this.restaurantService.getById(id).subscribe(data => {
          loader.dismiss();
          this.app.getRootNav().push(RestaurantPage, {
            id: id
          })
        }, error => {
          if(error.status === 401){
            this.presentToast("Sua sessão expirou");
            this.goToLogout();
          }
        });
      }, error => {
          console.log(error);
          loader.dismiss();
          this.presentToast("Desculpe. Tente novamente.");
       });
    }
  }

  findRestaurant() {

    let loader = this.loadingCtrl.create({
      content: 'Carregando...'
    });

    let ids = this.loginService.getUser().favorites_restaurants_ids;
    let array = [];

    for (var i = 0; i < ids.length; i++) {
      array.push(ids[i].restaurant);
    }

    ids = this.restaurantService.getByFavoriteAlreadyLoad(array);

    if(ids && ids.length){
      // Show the popup
      loader.present();
      loader.present().then(() => {
        this.restaurantService.getByIds(array).subscribe(result =>{ 
          loader.dismiss();
          this.restaurants = this.restaurantService.getFavoritesRestaurants();
        }, error => {
          if(error.status === 401){
            this.presentToast("Sua sessão expirou");
            this.goToLogout();
          }
        });
      });
    }else{
      this.restaurants = this.restaurantService.getFavoritesRestaurants();
    }
    
  }

  // Chama Tela para selecionar endereço.
  getAddressByGoogleMaps() {
    this.app.getRootNav().push(LocationPage);
  }

  getDistance(distance){
    return distance ? distance : 0.00;
  }

  ionViewWillEnter() {
    this.location = this.loginService.getUser().location;
    this.findRestaurant();
  }
  presentToast(message) {
    let toast = this.toastCtrl.create({
      message: message,
      duration: 3000,
      position: 'top'
    });

    toast.present();
  }

  // go to login page
  goToLogout() {
    this.updateUser();
    this.nav.push(LoginPage);
  }
  // Remove o usuário do banco de dados local
  private updateUser() {
      return this.usersProvider.update({id: 1, json: '{}'});
  }

  getCircle(index, size) {
    return index + 1 < size ? '• ' : '';
  }

  getFrete(restaurant) {

    let frete = this.cartService.calculateFrete(restaurant.frete,
      restaurant.frete_km, restaurant.km_max,
      restaurant.km_normal, restaurant.distance);

    return typeof frete === 'string';

  }
}


