import {
	Component
} from '@angular/core';
import {
	NavController,
	NavParams
} from 'ionic-angular';

import {
	MainTabsPage
} from "../main-tabs/main-tabs";


import { GoogleAnalytics } from '@ionic-native/google-analytics';

import { StatusBar } from '@ionic-native/status-bar';
/**
 * Generated class for the FinishOrderSuccessPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@Component({
	selector: 'page-finish-order-success',
	templateUrl: 'finish-order-success.html',
})
export class FinishOrderSuccessPage {

	orderNumber:number;
	delivery:string;
	constructor(public navCtrl: NavController,
		private statusBar: StatusBar,
		public ga: GoogleAnalytics,
		public navParams: NavParams) {

		// let status bar overlay webview
	    this.statusBar.overlaysWebView(false);

	    // set status bar to white
	    this.statusBar.backgroundColorByHexString('#ffb42d');
	    
		this.ga.trackView('Tela pedido realizado');

		this.orderNumber = navParams.get('id');
		if(navParams.get('delivery_min') != null || navParams.get('delivery_min') != 'null' ){
			this.delivery = navParams.get('delivery_min') + " - " + navParams.get('delivery_max');
		}
	}

	goToMain() {
		this.navCtrl.setRoot(MainTabsPage);
	}

}


