import {
  Component
} from '@angular/core';
import {
  AlertController,
  LoadingController,
  NavParams,
  NavController,
  ToastController
} from 'ionic-angular';

import {
  HistoryOrderService
} from '../../../services/history-order-service';

import {
  CallNumber
} from '@ionic-native/call-number';

import {
  LaunchNavigator
} from '@ionic-native/launch-navigator';

import {
  LoginPage
} from "../../login/login";
import {UsersProvider } from '../../../providers/users/users';

import { GoogleAnalytics } from '@ionic-native/google-analytics';

import { Events } from 'ionic-angular';

import { StatusBar } from '@ionic-native/status-bar';
/*
  Generated class for the LoginPage page.

  See http://ionicframework.com/docs/v2/components/#navigation for more info on
  Ionic pages and navigation.
*/
@Component({
  selector: 'page-history-detail',
  templateUrl: 'history-detail.html',
})
export class HistoryDetailsPage {
  order: any;
  statusName: any = ['', 'PENDENTE - AGUARDANDO CONFIRMAÇÃO',
    'EM PREPARO - JÁ FOI CONFIRMADO',
    'SAIU PARA ENTREGA',
    'CONCLUÍDO - PEDIDO ENTREGUE',
    'CANCELADO - PEDIDO CANCELADO',
    'PRONTO - CLIENTE VEM BUSCAR',
  ];

  logs:any;
  constructor(public navParams: NavParams,
    public nav: NavController,
    private historyOrderService: HistoryOrderService,
    private alertCtrl: AlertController,
    private callNumber: CallNumber,
    private launchNavigator: LaunchNavigator,
    private toastCtrl: ToastController,
    public ga: GoogleAnalytics,
    private usersProvider: UsersProvider,
    private statusBar: StatusBar,
    public events: Events,
    private loadingCtrl: LoadingController) {

    // let status bar overlay webview
    this.statusBar.overlaysWebView(false);

    // set status bar to white
    this.statusBar.backgroundColorByHexString('#ffb42d');

    this.ga.trackView('Tela Acompanhamento do Pedido');

    let orderId = JSON.parse(navParams.get('orderId'));
    this.order = this.historyOrderService.getById(orderId);

    this.order.statusName = this.statusName[this.order.status];

    this.logs = JSON.parse(this.order.log);

    let env = this;
    events.subscribe('order:update', (response, time) => {
      env.getOrderById(response.orderId);
    });
  }

  getOrderById(id) {
    // Create the popup
    let loader = this.loadingCtrl.create({
      content: 'Atualizando pedido...'
    });
    // Show the popup
    loader.present();
    loader.present().then(() => {
      this.historyOrderService.getRefreshById(id).subscribe(result => {
        try{
          this.order = result.data;
          this.order.statusName = this.statusName[this.order.status];
          this.logs = JSON.parse(this.order.log);
          loader.dismiss();
        }catch(ex){
          loader.dismiss();
        }
      }, error => {
        console.log(error);
        loader.dismiss();
        if(error.status === 401){
          this.presentToast("Sua sessão expirou");
          this.goToLogout();
        }
      });
    });
  }

  getPayment(payment) {
    var name = payment.name
    var card = payment.card ? payment.card : '';
    var online = card.length && payment.is_online === 0 ? 'máquina' : '';
    return name + ' ' + card + ' ' + online;
  }

  getFrete(frete) {
    if (frete && !isNaN(frete)) {
      return "R$" + parseFloat(frete).toFixed(2);
    } else {
      return 'Grátis';
    }
  }

  getTotal(total){
    if(typeof total === 'string'){
      return parseFloat(total).toFixed(2)
    }
    return total;
  }

  getPriceItem(price) {
    return price ? parseFloat(price) : 0.00;
  }

  isDelivery() {
    if (parseInt(this.order.flag_delivery) > 0) {
      return true;
    }
    return false;
  }

  orderCancel(note) {

    // 5 -> CANCELADO - PEDIDO CANCELADO
    let order = {
      userName: this.order.orders.user.first_name + " " + this.order.orders.user.last_name,
      status: 5,
      note: note,
      orderId: this.order.id
    }

    // Create the popup
    let loader = this.loadingCtrl.create({
      content: 'Aguarde o cancelamento...'
    });
    // Show the popup
    loader.present();
    loader.present().then(() => {
      this.historyOrderService.cancelOrder(order).subscribe(result => {
        if (result.status) {
          this.order.status = result.orderStatus;
          this.order.note = result.note;
          this.presentToast(result.message);
        } else {
          this.presentAlert(result.message);
        }
        loader.dismiss();
      }, error => {
        console.log(error);
        // remove the popup
        loader.dismiss();
        if(error.status === 401){
          this.presentToast("Sua sessão expirou");
          this.goToLogout();
        }
      });
    });
  }

  isDiscount(discount){
    try{
      discount = JSON.parse(discount);
      return discount && discount.status; 
    }catch(ex){
      return false;
    }
  }

  getFormatDate() {

    let stringDate = this.logs.status[0].updatedAt;
    stringDate = stringDate.substring(0, stringDate.lastIndexOf("-"));
    
    let split = stringDate.split('T');

    let date = split[0].split('-');
    let time = split[1].split('.');

    return date[2]+'/'+date[1]+'/'+date[0]+' '+time[0];
  }

  ionViewDidEnter() {
    if (this.order && this.order.id) {
      this.order = this.historyOrderService.getById(this.order.id);
    }
  }

  presentAlert(subTitle) {
    let alert = this.alertCtrl.create({
      title: 'Alerta',
      subTitle: subTitle,
      buttons: ['Ok']
    });
    alert.present();
  }

  presentToast(message) {
    let toast = this.toastCtrl.create({
      message: message,
      duration: 3000,
      position: 'top'
    });

    toast.present();
  }

  presentOrderCancel() {
    const alert = this.alertCtrl.create({
      title: 'CANCELAR PEDIDO',
      message: 'Você realmente deseja cancelar o seu pedido?',
      inputs: [{
        name: 'note',
        placeholder: 'Motivo do cancelamento'
      }],
      buttons: [{
        text: 'Não',
        role: 'cancel',
        handler: data => {
          console.log('Cancel clicked');
        }
      }, {
        text: 'Sim',
        handler: data => {
          if (data.note) {
            this.orderCancel(data.note);
          } else {
            this.presentAlert("Motivo do cancelamento é obrigatório");
          }
        }
      }]
    });
    alert.present();
  }

  showCallNumber(number) {
    this.callNumber.callNumber(number, true)
      .then(() => console.log('Launched dialer!'))
      .catch(() => console.log('Error launching dialer'));
  } 

  navigate() {
    let startLat = this.order.orders.address.latitude,
        startLng = this.order.orders.address.longitude,
        endLat = this.order.orders.restaurant.address.latitude,
        endLng = this.order.orders.restaurant.address.longitude;
    this.launchNavigator.navigate([endLat, endLng], {
      start: startLat+','+ startLng,
      app: this.launchNavigator.APP.GOOGLE_MAPS
    });
  }

  // go to login page
  goToLogout() {
    this.updateUser();
    this.nav.push(LoginPage);
  }
  // Remove o usuário do banco de dados local
  private updateUser() {
      return this.usersProvider.update({id: 1, json: '{}'});
  }

  getShowObservacao(order) {
    if(order.note && order.status !== 4 && order.status !== 6) {
      return true;
    }
    return false;
  }

  getStatus( status ) {
    return parseInt(status);
  }

  getDiscount(discount) {
    discount = JSON.parse(discount);
    if (discount.discount_type === "1" || discount.discount_type === true) {
      return discount.value + "%";
    } else {
      return "R$ " + discount.value;
    }
  }

}


