import {
	Component
} from '@angular/core';

import {
	App,
	AlertController,
	NavController,
	LoadingController,
	ToastController,
} from 'ionic-angular';

import {
	CouponService
} from "../../services/coupon.service";

import {
	ListRestaurantsPage
} from "./list-restaurants/list-restaurants";

import {
	WithoutInternetPage
} from "../without-internet/without-internet";

import {
	CheckInternet
} from '../../services/check-internet';

import {
	PromotionService
} from '../../services/promotion-service';

import {
	RestaurantService
} from '../../services/restaurant-service';

import {
  CartService
} from '../../services/cart-service';

import {
  LoginService
} from "../../services/login-service";

import {
	LoginPage
} from "../login/login";
import {
	UsersProvider
} from '../../providers/users/users';

import {
	RestaurantPage
} from "../restaurant/restaurant";

import { GoogleAnalytics } from '@ionic-native/google-analytics';

import { StatusBar } from '@ionic-native/status-bar';
/*
  Generated class for the LoginPage page.

  See http://ionicframework.com/docs/v2/components/#navigation for more info on
  Ionic pages and navigation.
*/
@Component({
	selector: 'page-promotions',
	templateUrl: 'promotions.html',
})
export class PromotionsPage {

	coupons: any;
	promotions: any;
	showMsgCoupon: boolean;
	showMsgPromotion: boolean;
  	
  	public showSpinner: boolean = true;
  	detailSegment:string = "s-coupon";
  	

	constructor(public app: App,
		public nav: NavController,
		private alertCtrl: AlertController,
		private couponService: CouponService,
		private toastCtrl: ToastController,
		private loadingCtrl: LoadingController,
		private checkInternet: CheckInternet,
		public ga: GoogleAnalytics,
		private statusBar: StatusBar,
		public restaurantService: RestaurantService,
		private promotionService: PromotionService,
		private loginService: LoginService,
		private cartService: CartService,
		private usersProvider: UsersProvider) {

		 // let status bar overlay webview
	    this.statusBar.overlaysWebView(false);

	    // set status bar to white
	    this.statusBar.backgroundColorByHexString('#ffb42d');

		this.ga.trackView('Tela de promoções');

		this.coupons = [];
		this.promotions = [];
		this.showMsgCoupon = false;
		this.showMsgPromotion = false;
	}

	ionViewDidEnter() {
		this.populate();
		this.populatePromotion();

		setInterval(function(){ this.coupons = this.coupons }, 1000);
	}

	ionViewWillLeave() {
		this.cartService.setPromotion({});
	}

	populate() {

		// Antes de tudo vericar se usuário possui Internet
		if (!this.checkInternet.isInternet()) {
			this.withoutInternet();
			return false;
		}

	    this.showSpinner = true;
	    this.couponService.populate().subscribe(result => {
	      this.coupons = result;
	      this.showMsgCoupon = false;
	      this.showSpinner = false;
	    }, error => {
	      this.showSpinner = false;
	      this.showMsgCoupon = false;
	      if(error.status === 401){
	        this.presentToast("Sua sessão expirou");
	        this.goToLogout();
	      }
	    });

	    
	}

	populatePromotion() {

		this.showSpinner = true;
	    this.promotionService.populate().subscribe(result => {
	      this.promotions = result;
	      this.showMsgPromotion = true;
	      if(!this.promotions.length){
	      	this.showMsgPromotion = false;
	      	this.detailSegment = "s-coupon";
	      }else{
	      	this.detailSegment = "s-promotion";
	      }
	      this.showSpinner = false;
	    }, error => {
	      this.showSpinner = false;
	      this.showMsgPromotion = false;
	      if(error.status === 401){
	        this.presentToast("Sua sessão expirou");
	        this.goToLogout();
	      }
	    });
	}

	getDistance(distance) {
		return distance ? distance : 0.00;
	}


	// view restaurant detail
	viewRestaurant(coupon) {
		let id = coupon.restaurants_id;
		this.cartService.setDiscount(coupon);

		let restaurant = this.restaurantService.getExistById(id);
		//Caso já tenha carregado o restaurante pelo Id não é necessário carregar novamente
		if (restaurant && restaurant.Cuisines) {
			this.app.getRootNav().push(RestaurantPage, {
				id: id,
				promotion: true
			})
		} else {
			// Antes de tudo vericar se usuário possui Internet
			if (!this.checkInternet.isInternet()) {
				this.withoutInternet();
				return false;
			}

			let loader = this.loadingCtrl.create({
				content: 'Carregando...'
			});
			// Show the popup
			loader.present();
			loader.present().then(() => {
				this.restaurantService.getById(id).subscribe(data => {
					loader.dismiss();
					this.app.getRootNav().push(RestaurantPage, {
						id: id,
						promotion: true
					})
				}, error => {
					console.log(error);
					loader.dismiss();
				});
			}, error => {
				console.log(error);
				loader.dismiss();
				if (error.status === 401) {
					this.presentToast("Sua sessão expirou");
					this.goToLogout();
				} else {
					this.presentToast("Desculpe. Tente novamente.");
				}
			});
		}
	}
	// view restaurant detail
	applyPromotion(promotion) {
		let id = promotion.id;

		if(promotion.only_new_user === 1 && !this.loginService.getUser().isNew){
	      this.presentConfirm(promotion);
	    }else{
	    	this.cartService.setPromotion(promotion);
			this.app.getRootNav().push(ListRestaurantsPage, { promotionId: id });
		}
	}

	goToRestaurant(promotion) {
		this.app.getRootNav().push(ListRestaurantsPage, { promotionId: promotion.id })	
	}

	presentToast(message) {
		let toast = this.toastCtrl.create({
			message: message,
			duration: 3000,
			position: 'top'
		});

		toast.present();
	}
	// Tela de sem acesso a internet
	withoutInternet() {
		this.app.getRootNav().push(WithoutInternetPage);
	}

	getCoupon(coupon) {
		if (coupon.discount_type == 0) {
			return "R$ " + coupon.price + " de desconto ";
		} else {
			return coupon.price + " % de desconto";
		}
	}

	// go to login page
	goToLogout() {
		this.updateUser();
		this.nav.push(LoginPage);
	}
	// Remove o usuário do banco de dados local
	private updateUser() {
		return this.usersProvider.update({
			id: 1,
			json: '{}'
		});
	}

	isExpiration(coupon){
		if(coupon.expiration){
			let dateString = coupon.expiration.substr(0,(coupon.expiration.length - 5));
			let date = new Date(dateString);
			let dateNow = new Date();

			let result = date.getTime() - dateNow.getTime();
			if(coupon.privated == 0 && result > 0){
				this.showMsgCoupon = true;
			}
			return date.getTime() - dateNow.getTime();
		}else{
			return -1;
		}
	}

	getExpirationDate(coupon) {
		let ms = this.isExpiration(coupon);

		return this.msToTime(ms);
	}

	msToTime(duration:number) {

        let seconds = (duration/1000)%60,
        	minutes = (duration/(1000*60))%60
            , hours = (duration/(1000*60*60))%24;

        hours = parseInt(hours+"");
        minutes = parseInt(minutes+"");
        seconds = parseInt(seconds+"");

        let h = (hours < 10) ? "0" + hours : hours;
        let m = (minutes < 10) ? "0" + minutes : minutes;
        let s = (seconds < 10) ? "0" + seconds : seconds;

        return h + ":" + m + ":" + s;
    }

    presentConfirm(promotion) {
	    let alert = this.alertCtrl.create({
	      title: 'Válido para novos clientes',
	      message:'Promoção não será aplicada. Deseja continuar?',
	      buttons: [{
	        text: 'Não',
	        role: 'cancel',
	        handler: () => {
	          console.log('Cancel clicked');
	        }
	      }, {
	        text: 'Sim',
	        handler: () => {
	          this.goToRestaurant(promotion);
	        }
	      }]
	    });
	    alert.present();
	}
}



