import {
  Injectable
} from "@angular/core";
import {
  CONFIG
} from "./config";
import {
  Http,
  Headers
} from '@angular/http';
import 'rxjs/Rx';
import {
  Observable
} from 'rxjs';

import {
  LoginService
} from "./login-service";
@Injectable()
export class UserService {
  private restaurants: any;
  wallet:any;
  constructor(public loginService: LoginService,
    public http: Http) {}

  getAll() {
    return this.restaurants;
  }

  recoverPassword(email) {
    return this.http.get(CONFIG.url + "/users/forgot-password/" + email)
      .map(res => res.json())
      .catch(error => {
        return Observable.throw(error);
      });
  }

  changePassword(user) {
    let headers = new Headers();
    headers.append('authorization', this.getToken());
    return this.http.post(CONFIG.url + "/users/password", user, {
        headers: headers
      })
      .timeout(10000)
      .map(res => res.json())
      .catch(error => {
        return Observable.throw(error);
      });
  }

  updatePhone(phone) {
    let user = this.getUser();
    let request = {
      id: user.id,
      phone: phone
    }
    this.setPhone("", phone);
    let headers = new Headers();
    headers.append('authorization', this.getToken());
    return this.http.put(CONFIG.url + "/users/phone", request, {
        headers: headers
      })
      .timeout(10000)
      .map(res => res.json())
      .catch(error => {
        return Observable.throw(error);
      });
  }

  favouriteRestaurant(favouriteRestaurant) {
    let headers = new Headers();
    headers.append('authorization', this.getToken());
    return this.http.put(CONFIG.url + "/users/favorite", favouriteRestaurant, {
        headers: headers
      })
      .timeout(10000)
      .map(res => res.json())
      .catch(error => {
        return Observable.throw(error);
      });
  }

  sendEmailToSaciaFome(user) {
    let headers = new Headers();
    headers.append('authorization', this.getToken());
    return this.http.post(CONFIG.url + "/help", user, {
        headers: headers
      })
      .timeout(10000)
      .map(res => res.json())
      .catch(error => {
        return Observable.throw(error);
      });
  }

  getBalance() {
    let user = this.getUser();
    let headers = new Headers();
    headers.append('authorization', this.getToken());
    return this.http.get(`${CONFIG.url}/wallet/user/${user.id}`, {
        headers: headers
      })
      .timeout(10000)
      .map(res => {
        this.wallet = res.json()
        return res.json();
      })
      .catch(error => {
        return Observable.throw(error);
      });
  }

  getToken() {
    return this.loginService.getToken();
  }

  getWallet() {
    return this.wallet
  }

  sendSms(phone) {
    
    let user = this.loginService.getUser();

    let json = {
      id: user.id,
      phone: phone
    }
    let headers = new Headers();
    headers.append('authorization', this.getToken());
    return this.http.post(`${CONFIG.url}/sms`, json, {
        headers: headers
      })
      .map(res => res.json())
      .catch(error => {
        return Observable.throw(error);
      });

  }

  updateActivationPhone(){
    let user = this.loginService.getUser();

    let json = {
      id: user.id
    }
    let headers = new Headers();
    headers.append('authorization', this.getToken());
    return this.http.put(`${CONFIG.url}/users/activation`, json, {
        headers: headers
      })
      .map(res => res.json())
      .catch(error => {
        return Observable.throw(error);
      });
  }

  getUser() {
    return this.loginService.getUser();
  }

  setPhone(countryCode, phone) {
    let user = this.loginService.getUser();
    user.country_code = countryCode;
    user.phone = phone;
    this.loginService.setUser(user);
    this.loginService.setUserDatabase(user);
  }

}



