import {
	Component
} from '@angular/core';
import {
	App,
	NavController,
	NavParams,
	ViewController
} from 'ionic-angular';

import { Market } from '@ionic-native/market';

// import pages
import {
  MainTabsPage
} from "../main-tabs/main-tabs";

import {
  AppVersionService
} from '../../services/app-version-service';

import { GoogleAnalytics } from '@ionic-native/google-analytics';

import { StatusBar } from '@ionic-native/status-bar';
/**
 * Generated class for the AppVersionPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@Component({
	selector: 'page-app-version',
	templateUrl: 'app-version.html',
})
export class AppVersionPage {

	private newVersion:number;
  	private appVersion:number;

	constructor(public app: App,
		public nav: NavController, 
		public navParams: NavParams,
		private viewCtrl: ViewController,
		private market: Market,
		public ga: GoogleAnalytics,
		private statusBar: StatusBar,
		private appVersionService: AppVersionService) {

		// let status bar overlay webview
		this.statusBar.overlaysWebView(false);

		// set status bar to white
		this.statusBar.backgroundColorByHexString('#ffb42d');

		this.ga.trackView('Tela Atualizar Aplicativo');

		this.newVersion = this.appVersionService.getNewVersion();
		this.appVersion = this.appVersionService.getAppVersion();
	}

	ionViewDidLoad() {
		console.log('ionViewDidLoad AppVersionPage');
	}

	ionViewWillEnter() {
		this.viewCtrl.showBackButton(false);
	}

	openMarket() {
		this.market.open('com.ionicframework.saciafome148065');
	}

	//go to login
	goToLogin() {
		this.app.getRootNav().push(MainTabsPage);
	}
}


