import {
  Component
} from '@angular/core';
import {
  AlertController,
  NavController,
  LoadingController,
  ToastController
} from 'ionic-angular';
import {
  FormBuilder,
  Validators
} from '@angular/forms';

import {
  CartService
} from '../../services/cart-service';

import {
  RegisterService
} from '../../services/register-service';

import {
  LoginService
} from '../../services/login-service';

import {
  TermsPage
} from "../terms/terms";

import {
  ConstructionPage
} from "../construction/construction";

import {
  UsersProvider,
  User
} from '../../providers/users/users';


import {
  CheckInternet
} from '../../services/check-internet';

import { GoogleAnalytics } from '@ionic-native/google-analytics';

import { StatusBar } from '@ionic-native/status-bar';

@Component({
  selector: 'page-register',
  templateUrl: 'register.html',
})
export class RegisterPage {

  register: any;
  profile: any;
  showPasswordIsChecked:boolean = false;
  constructor(public nav: NavController,
    private registerService: RegisterService,
    private usersProvider: UsersProvider,
    private cartService: CartService,
    private loginService: LoginService,
    private toastCtrl: ToastController,
    private alertCtrl: AlertController,
    private loadingCtrl: LoadingController,
    private checkInternet: CheckInternet,
    private statusBar: StatusBar,
    public ga: GoogleAnalytics,
    public formBuilder: FormBuilder) {

    // let status bar overlay webview
    this.statusBar.overlaysWebView(false);

    // set status bar to white
    this.statusBar.backgroundColorByHexString('#ffb42d');

    this.ga.trackView('Tela Registrar');
    
    this.showPasswordIsChecked = false;
    this.profile = {
      country_code: 55
    };

    this.register = this.formBuilder.group({
      first_name: ['', Validators.compose([Validators.minLength(3), Validators.required])],
      last_name: ['', Validators.compose([Validators.minLength(3), Validators.required])],
      email: ['', Validators.compose([Validators.email, Validators.required])],
      country_code: ['', Validators.compose([Validators.minLength(2),Validators.required])],
      phone: ['', Validators.compose([Validators.minLength(9),Validators.required])],
      password: ['', Validators.compose([Validators.minLength(6),Validators.required])],
      repeatPassword: ['', Validators.compose([Validators.minLength(6),Validators.required])],
      showPassword: ['']
    });
  }

  // register
  signUp() {
    // Antes de tudo vericar se usuário possui Internet
    if (!this.checkInternet.isInternet()) {
      this.presentToast("Sem acesso a internet");
      return false;
    }

    if (!this.validateEmail(this.profile.email)) {
      this.presentAlert("Alerta", "Endereço de e-mail inválido");
      return;
    }
    if (this.profile.password.length < 6) {
      this.presentAlert("Alerta", "A senha deve ter pelo menos 6 caracteres.");
      return;
    }
    if (this.profile.password !== this.profile.repeatPassword) {
      this.presentAlert("Alerta", "Senhas não são iguais.");
      return;
    }
    this.ga.trackEvent('Click', 'Botão Registro', null, null);
    // Create the popup
    let loader = this.loadingCtrl.create({
      content: 'Aguarde...'
    });
    // Show the popup
    loader.present();
    loader.present().then(() => {
      this.registerService.save(this.profile).subscribe(result => {
        // remove the popup
        loader.dismiss();
        if (result.status) {
          if(result.data.information && result.data.information.status){
            this.presentAlert("", result.data.information.msg);  
          }else{
            this.presentToast(result.message);
          }
          this.register.reset();
          this.redirectMainPage(result.data);
        } else {
          this.presentAlert("Alerta", result.message)
        }
      }, error => {
        console.log(error);
        if(error.name === "TimeoutError"){
          this.presentToast('Tente novamente!');
        }
        // remove the popup
        loader.dismiss();
      });
    });
  }

  validateEmail(email) {
    // Expressão para formato de email
    var re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    // Valida se email é valido
    if (!re.test(email)) {
      return false;
    }
    return true;
  }

  redirectMainPage(user) {

    // Seta o Usuário no LoginService
    this.loginService.setUser(user);
    
    //Após realizar o cadastro, salva o usuário no banco de dados local.
    this.saveUserDatabase(user);

    // Seta o usuário no Carrinho de Compras.
    this.cartService.setUser(user);

    this.nav.pop();
  }

  presentAlert(title,subTitle) {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: subTitle,
      buttons: ['Ok']
    });
    alert.present();
  }

  presentToast(message) {
    let toast = this.toastCtrl.create({
      message: message,
      duration: 3000,
      position: 'top'
    });

    toast.present();
  }

  saveUserDatabase(user) {
    let model = new User();
    model.id = 1;
    model.json = JSON.stringify(user);

    this.updateUser(model)
      .then(() => {
        console.log("Usuário inserido no banco de dados com sucesso");
      })
      .catch(() => {
        console.log("Erro ao tentar salvar o usuário no banco de dados local");
      });
  }

  // Salva o usuário no banco de dados local
  private updateUser(model) {
    return this.usersProvider.update(model);
  }


  // go to term page
  getTerm() {
    this.nav.push(TermsPage);
  }

  // go to term page
  getConstruction() {
    this.nav.push(ConstructionPage);
  }

}


