import {
	Component
} from '@angular/core';
import {
	NavController,
	NavParams,
	LoadingController,
	ToastController,
	ViewController
} from 'ionic-angular';

import {
	LoginService
} from "../../services/login-service";

import {
	RestaurantService
} from '../../services/restaurant-service';

import {
	Http
} from '@angular/http';
import 'rxjs/Rx';

import {
	Geolocation
} from '@ionic-native/geolocation';
import {
	LocationAccuracy
} from '@ionic-native/location-accuracy';

import {
	NewAddressPage
} from '../address/new-address/new-address';
import {
	AddressService
} from '../../services/address-service';

import {
	LoginPage
} from "../login/login";
import {UsersProvider } from '../../providers/users/users';

import {
	CheckInternet
} from '../../services/check-internet';

import { StatusBar } from '@ionic-native/status-bar';
/**
 * Generated class for the LocationPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@Component({
	selector: 'page-location',
	templateUrl: 'location.html',
})
export class LocationPage {

	placesService: any;
	private location: any;
	address: any;
	showSpinner: boolean = false;
	isLogged:boolean = false;
	constructor(public nav: NavController,
		public toastCtrl: ToastController,
		public http: Http,
		private geolocation: Geolocation,
		public navParams: NavParams,
		public viewCtrl: ViewController,
		private checkInternet: CheckInternet,
		private loadingCtrl: LoadingController,
		private loginService: LoginService,
		private locationAccuracy: LocationAccuracy,
		private statusBar: StatusBar,
		public addressService: AddressService,
		private usersProvider: UsersProvider,
		private restaurantService: RestaurantService) {

		// let status bar overlay webview
	    this.statusBar.overlaysWebView(false);

	    // set status bar to white
	    this.statusBar.backgroundColorByHexString('#ffb42d');

	}

	ngOnInit() {

	}

	// Busca todos os estabelecimentos pelo endereço que esta selecionado pelo usuário
	findRestaurant() {
		// Antes de tudo vericar se usuário possui Internet
		if (!this.checkInternet.isInternet()) {
			this.presentToast("Sem acesso a internet");
			return false;
		}

		let loader = this.loadingCtrl.create({
			content: 'Carregando...'
		});

		// Show the popup
		loader.present();
		loader.dismiss().then(() => {
			this.restaurantService.getInitialHome().subscribe(result => {
				this.goToHomePage();
			}, error => {
				if(error.status === 401){
					this.goToLogout();
				}
				console.log(error);
			});
		});
	}

	//Utiliza o plugin locationAccurancy para pegar a posição do usuário pelo GPS do celular.
	closeToMe() {
		// Antes de tudo vericar se usuário possui Internet
		if (!this.checkInternet.isInternet()) {
			this.presentToast("Sem acesso a internet");
			return false;
		}

		let loader = this.loadingCtrl.create({
			content: 'Carregando...'
		});

		// Show the popup
		loader.present();
		loader.dismiss().then(() => {

			// the accuracy option will be ignored by iOS
			this.locationAccuracy.request(this.locationAccuracy.REQUEST_PRIORITY_HIGH_ACCURACY).then(
				() => {
					this.location = {};
					this.geolocation.getCurrentPosition().then((resp) => {
						this.location.latitude = resp.coords.latitude;
						this.location.longitude = resp.coords.longitude;
						if (this.location.latitude && this.location.longitude) {
							this.getAddress(this.location.latitude, this.location.longitude);
						}
					}).catch((error) => {
						console.log('Error getting location', error);
						this.presentToast('Desculpe. Não conseguimos a sua localização. Tente novamente!');
					});
				},
				error => {
					console.log('Error requesting location permissions', error);
					this.presentToast('Desculpe. Não conseguimos a sua localização. Tente novamente!');
				}
			);
		});

	}
	// Busca o endereço pela Latitude e Longitude
	getAddress(lat, lng) {
		// Antes de tudo vericar se usuário possui Internet
		if (!this.checkInternet.isInternet()) {
			this.presentToast("Sem acesso a internet");
			return false;
		}

		let loader = this.loadingCtrl.create({
			content: 'Carregando...'
		});

		// Show the popup
		loader.present();
		loader.dismiss().then(() => {
			this.getLocationByLatAndLng(lat, lng, loader).subscribe(result => {
				// Seta o endereço no carrinho de compras
				//this.setAddress(address);
			}, error => {
				console.log(error);
				loader.dismiss();
			});
		});
	}

	// Busca o endereço na API do Google Maps pela latitude e longitude
	getLocationByLatAndLng(lat, lng, loader) {

		let env = this;
		return this.http.get("https://maps.googleapis.com/maps/api/geocode/json?latlng=" + lat + "," + lng + "&sensor=true&key=AIzaSyAnIl-LlxxlgkQ5iD9MiHdEj4AJ-g9K8nc")
			.map(res => {
				let formatJson = JSON.parse(res["_body"]);

				// Convert resposta da API do google em um objeto
				if(formatJson.results.length){
					let address = env.loginService.mountAddress(formatJson.results[0].address_components, env.location);

					// Seta o endereço recebido da API
					env.loginService.setLocation(address);

					env.findRestaurant();
				}else{
					env.presentToast("Desculpe, não conseguimos a sua localização. Tente novamente.");
				}
				loader.dismiss();
				return res;
			})
			.catch(error => {
				loader.dismiss();
				return error;
			});
	}

	// Busca todos os endereços do usuário
	findAddress() {
		// Antes de tudo vericar se usuário possui Internet
		if (!this.checkInternet.isInternet()) {
			this.presentToast("Sem acesso a internet");
			return false;
		}

		// Create the popup
		let loader = this.loadingCtrl.create({
			content: 'Buscando endereços...'
		});
		this.showSpinner = true;
		// Show the popup
		loader.present();
		loader.dismiss().then(() => {
			this.addressService.findById(this.loginService.getUser().id).subscribe(data => {
				this.address = data.data;
				this.showSpinner = false;
			}, error => {
				this.showSpinner = false;
				if(error.status === 401){
					this.goToLogout();
				}
				console.log(error);
			});
		});
	}

	// Abre a tela para cadastrar novo endereço
	newAddress() {
		this.nav.push(NewAddressPage, {
			userId: this.loginService.getUser().id
		});
	}


	// Seta o endereço escolhido para buscar os restaurantes que estão proximos do endereço selecionado
	setAddress(address) {
		let location = {
			latitude: address.latitude,
			longitude: address.longitude,
			street: address.street + ", " + address.number,
			complement: address.complement,
			neighborhood: address.neighborhood,
			city: address.city,
			state: address.state,
			cep: address.cep
		}
		this.loginService.setLocation(location);
		this.findRestaurant();
	}

	// Redireciona para tela principal
	goToHomePage() {
		// Redirecionado para tela principal
		this.nav.pop();
	}

	// Assim que entrar na tela busca todos os endereços daquele usuário
	ionViewWillEnter() {
		// Antes de tudo vericar se usuário possui Internet
		if (!this.checkInternet.isInternet()) {
			this.presentToast("Sem acesso a internet");
			return false;
		}
		this.isLogged = this.loginService.isLogged();
		this.address = this.addressService.getAll();
		if (this.isLogged && !this.address.length) {
			this.findAddress();
		}
	}

	presentToast(message) {
		let toast = this.toastCtrl.create({
			message: message,
			duration: 3000,
			position: 'top'
		});

		toast.present();
	}

	// go to login page
	goToLogout() {
		this.updateUser();
	    this.nav.push(LoginPage);
	}
	// Remove o usuário do banco de dados local
	private updateUser() {
	    return this.usersProvider.update({id: 1, json: '{}'});
	}
}



