import {
  Component
} from '@angular/core';
import {
  App,
  NavController,
  ToastController
} from 'ionic-angular';

import {
  RatingPage
} from '../rating/rating';
import {
  HistoryDetailsPage
} from './details/history-detail';
import {
  HistoryOrderService
} from '../../services/history-order-service';

import {
  WithoutInternetPage
} from "../without-internet/without-internet";

import {
  CheckInternet
} from '../../services/check-internet';

import {
  LoginPage
} from "../login/login";
import {UsersProvider } from '../../providers/users/users';

import { GoogleAnalytics } from '@ionic-native/google-analytics';

import {
  LoginService
} from "../../services/login-service";

import { StatusBar } from '@ionic-native/status-bar';
/*
  Generated class for the LoginPage page.

  See http://ionicframework.com/docs/v2/components/#navigation for more info on
  Ionic pages and navigation.
*/
@Component({
  selector: 'page-order',
  templateUrl: 'order.html',
})
export class OrderPage {
  orders: any;
  public showSpinner: boolean = false;
  isLogged:boolean = false;
  statusName: any = ['', 'PENDENTE - AGUARDANDO CONFIRMAÇÃO',
    'EM PREPARO - JÁ FOI CONFIRMADO',
    'SAIU PARA ENTREGA',
    'CONCLUÍDO - PEDIDO ENTREGUE',
    'CANCELADO - PEDIDO CANCELADO',
    'PRONTO - CLIENTE VEM BUSCAR',
  ];
  constructor(public app: App,
    public nav: NavController,
    private toastCtrl: ToastController,
    public historyOrderService: HistoryOrderService,
    private checkInternet: CheckInternet,
    private usersProvider: UsersProvider,
    private loginService: LoginService,
    private statusBar: StatusBar,
    public ga: GoogleAnalytics) {

    // let status bar overlay webview
    this.statusBar.overlaysWebView(false);

    // set status bar to white
    this.statusBar.backgroundColorByHexString('#ffb42d');
    
    this.ga.trackView('Tela Historico Pedidos');
  }

  findHistoryOrder() {
    // Antes de tudo vericar se usuário possui Internet
    if (!this.checkInternet.isInternet()) {
      this.withoutInternet();
      return false;
    }

    this.showSpinner = true;
    this.historyOrderService.getByUser().subscribe(data => {
      // remove the popup
      this.orders = data.data;
      for (var i = 0; i < this.orders.length; i++) {
        if (this.orders[i].status === "4" && !this.orders[i].orders.rating) {
          this.getRating(this.orders[i]);
          break;
        }
      }
      this.showSpinner = false;
    }, error => {
      console.log(error);
      this.showSpinner = false;
      if(error.status === 401){
        this.presentToast("Sua sessão expirou");
        this.goToLogout();
      }
    });
  }

  getTotal(total){
    if(typeof total === 'string'){
      return parseFloat(total).toFixed(2)
    }
    return total;
  }

  getDetails(order) {
    this.nav.push(HistoryDetailsPage, {
      orderId: order.id
    });
  }

  getOrderStatus(status) {
    return this.statusName[status];
  }

  isDelivery(order) {

    if (parseInt(order.flag_delivery) > 0) {
      return true;
    }
    if (parseInt(order.flag_delivery) > 0 && parseInt(order.status) < 4) {
      return true;
    }
    return false;
  }

  getRating(order) {

    this.nav.push(RatingPage, {
      order: JSON.stringify(order)
    });
  }

  ionViewWillEnter() {
    console.log("Entrei");
    // Antes de tudo vericar se usuário possui Internet
    if (!this.checkInternet.isInternet()) {
      this.withoutInternet();
      return false;
    }
    this.isLogged = this.loginService.isLogged();
    if(this.isLogged){
      this.findHistoryOrder();
    }else{
      this.nav.push(LoginPage, {
        backButton: true
      });
    }
  }


  presentToast(message) {
    let toast = this.toastCtrl.create({
      message: message,
      duration: 3000,
      position: 'top'
    });

    toast.present();
  }

  getMedia(rate) {
    if (rate.rate_delivery) {
      let media = (rate.rate_custo + rate.rate_food + rate.rate_delivery) / 3;
      return media.toFixed(2);
    } else {
      let media = (rate.rate_custo + rate.rate_food) / 2;
      return media.toFixed(2);
    }
  }
  withoutInternet() {
    this.app.getRootNav().push(WithoutInternetPage);
  }

  // go to login page
  goToLogout() {
    this.updateUser();
      this.app.getRootNav().setRoot(LoginPage, {
        backButton: true
      });
  }
  // Remove o usuário do banco de dados local
  private updateUser() {
      return this.usersProvider.update({id: 1, json: '{}'});
  }

}


