import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';

import {
  LoginPage
} from "../login/login";

import {Platform} from 'ionic-angular';


/**
 * Generated class for the ConstructionPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@Component({
  selector: 'page-construction',
  templateUrl: 'construction.html',
})
export class ConstructionPage {

  platform:any;
  constructor(public nav: NavController, public navParams: NavParams, platform: Platform) {
    this.platform = platform;
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ConstructionPage');
  }

  // go to login page
  getLogin() {
    this.nav.push(LoginPage);
  }

  exitApp(){
     this.platform.exitApp();
  }

}



