import {Injectable} from "@angular/core";
import {CONFIG} from "./config";
import { Http, Headers } from '@angular/http';
import 'rxjs/Rx';
import { Observable } from 'rxjs';

import {LoginService} from "./login-service";

@Injectable()
export class CategoryService {
  private categories:any;

  constructor(public http: Http,
    public loginService:LoginService) {
    this.http = http;
    this.categories = [];
  }

  populate() {
    let headers = new Headers();
    headers.append('authorization', this.getToken());
    return this.http.get(CONFIG.url + "/categories", {headers: headers})
      .map(res => {
        if(!res.json() || !res.json().status){
          throw { status: 401, message: "O token temporário está expirado. Deve refazer o login novamente."};
        }
        this.categories = res.json().data;
        return res.json().data;
      })
      .catch(error => {
        return Observable.throw(error);
      });
  }

  getAll() {
    return this.categories;
  }
  remove(item) {
    this.categories.splice(this.categories.indexOf(item), 1);
  }

  getToken() {
    return this.loginService.getToken();
  }
}



